import db from 'libs/db';
import React from 'react';
import { Button, Modal, Spinner } from 'react-bootstrap';
import { FiInfo } from 'react-icons/fi';
import { toast } from 'react-toastify';
import AdminServices from 'services/api/AdminServices';
import BottomBackNextBtn from 'views/mobileView/components/atoms/BottomBackNextBtn';
import { dropdownCustom } from 'views/mobileView/components/atoms/DataCustomizer';
import FloatingTextInput from 'views/mobileView/components/atoms/FloatingTextInput';
import CustomDropDown from 'views/mobileView/components/molecules/DropDown/CustomDropDown';
import cssVar from 'views/mobileView/styles';
import colorsFinal from "../../../../../../theme/colors";
import ProfileCompleteHeader from '../../../../components/molecules/Headers/ProfileCompletionHeader';

export default function BankDetails(props, route) {


    const data = [
        { key: '1', gender: 'Savings' },
        { key: '2', gender: 'Current' }
    ]

    const data2 = [
        { key: '3', gender: 'NRE' },
        { key: '4', gender: 'NRO' }
    ]

    const [dshow, setdshow] = React.useState(false);
    const [ddata, setddata] = React.useState([]);

    const [loading, setloading] = React.useState(true);
    const [accountNo, setaccountNo] = React.useState('');
    const [caccountNo, setcaccountNo] = React.useState('');
    const [acctype, setacctype] = React.useState('');

    const [ifsc, setifsc] = React.useState('');
    const [banknm, setbanknm] = React.useState({});
    const [nextdisabled, setnextDisabled] = React.useState(true);
    const [bankList, setbankList] = React.useState([]);

    const [msg, setmsg] = React.useState(false);
    // helper
    const [helperType, sethelperType] = React.useState('');
    const [helperColor, sethelperColor] = React.useState('');
    const [helperText, sethelperText] = React.useState('');

    const checkValidation = () => {
        console.log(acctype);
        if (
            accountNo.length > 1
            && acctype !== 0
            && ifsc.length > 1
            && Object.entries(banknm).length !== 0
            && helperColor !== 'red'
        ) {
            // if (accountNo === caccountNo) {

            // } else {
            setnextDisabled(false);
            setmsg(false);
            // }
        } else {
            setnextDisabled(true);
        }
    };
    const checkOnBlurIFSC = async () => {
        try {
            const res = await AdminServices.verifyifsc(ifsc).then((res) => res);

            // console.log('sd', res.data);
            if (res.type) {
                const a = JSON.parse(res.data);

                if (a === 'Not Found') {
                    sethelperType('ifsc');
                    sethelperColor('red');
                    sethelperText('Enter a valid IFSC');
                } else {
                    // console.log('f000dgs');
                    sethelperType('ifsc');
                    sethelperColor('green');
                    sethelperText(`${`${a.BANK}, ${a.BRANCH}`}`);
                }
            } else {
                console.log('ERR : 1', res.msg);
                // setnextbtnPressed(false);
                toast.error("Try Again...")
            }
        } catch (error) {
            console.log('ERR : c', error);
            // setnextbtnPressed(false);
            toast.error("Try Again...")
        }
    };

    const getData = async () => {
        const res = await AdminServices.getBanks().then((res) => res);
        console.log('DATA : ', res);
        const data = await dropdownCustom(res.data, ['UID', 'Name']);
        // console.log('sd', data);
        setbankList(data);
    };

    React.useEffect(() => {
        checkValidation();
    });

    React.useEffect(() => {
        db.get(db.model.userData).then((u) => {
            if (u) {
                const r1 = u.bank;
                if (r1) {
                    setaccountNo(r1.accountNo || '');
                    setcaccountNo(r1.accountNo || '');
                    setacctype(r1.type || '');
                    setbanknm(r1.bankName || '');
                    setifsc(r1.ifsc || '');
                    setbankList(r1.allbank || []);
                    setloading(false);
                } else {
                    getData().then(() => {
                        setloading(false);
                    });
                }
            }
        });
        checkValidation();
    }, []);


    if (loading) {
        return (
            <div className="center-screen">
                <Spinner
                    as="span"
                    animation="grow"
                    size="sm"
                    role="status"
                    aria-hidden="true"
                />
            </div>
        );
    } else {
        return <div className="container">
            <ProfileCompleteHeader
                header="Bank Details"
                navigation={props.history}
                percentage={70}
                showInfo={
                    <FiInfo
                        className="mb-1"
                        size={15}
                        className="primary"
                        onClick={() => {
                            setddata(["", "Please use a bank account where you are either the sole holder or a primary/secondary holder in a joint account."]);
                            setdshow(true);
                        }}
                    />
                }
            />
            <Modal
                show={dshow}
                onHide={() => setdshow(!dshow)}
                backdrop="static"
                keyboard={false}
                size="sm"
                aria-labelledby="contained-modal-title-vcenter"
                centered
            >
                <Modal.Body>
                    <p className="textHead">{ddata[0]}</p>
                    <p className="textNormal">{ddata[1]}</p>
                </Modal.Body>
                <Button variant="light" style={{ color: colorsFinal.brandPrimary }} onClick={() => setdshow(!dshow)}>
                    Okay
                </Button>
            </Modal>

            <div className="mt-2 mb-2">
                <div className="floating-form">
                    <FloatingTextInput
                        label="Account Number"
                        type="password"
                        value={accountNo}
                        onChange={(text) => setaccountNo(text.target.value)}
                    />
                </div>
                
                <p className="textHead" style={{marginTop: 20}} >Bank account type</p>
                {props.location.state.selection === "nri" ?
                    data2.map((obj) => (
                        <div key={obj.key} style={{ display: 'flex', flexDirection: 'row' }}>

                            <div className="radio-item">
                                <input
                                    type="radio"
                                    id={obj.key}
                                    name="ritem"
                                    value={obj.key}
                                    onChange={(val) => {
                                        setacctype(val.target.value)
                                    }}
                                    className="textNormal"
                                    checked={acctype === obj.key}
                                />
                                <label className="textNormal radio-label" htmlFor={obj.key}>{obj.gender}</label>
                            </div>
                        </div>
                    ))
                    :
                    data.map((obj) => (
                        <div key={obj.key} style={{ display: 'flex', flexDirection: 'row' }}>
                            <div className="radio-item">
                                <input
                                    type="radio"
                                    id={obj.key}
                                    name="ritem"
                                    value={obj.key}
                                    onChange={(val) => {
                                        setacctype(val.target.value)
                                    }}
                                    className="textNormal"
                                    checked={acctype === obj.key}
                                />
                                <label className="textNormal radio-label" htmlFor={obj.key}>{obj.gender}</label>
                            </div>
                        </div>
                    ))
                }
                <div className="floating-form">
                    <FloatingTextInput
                        value={ifsc}
                        className="uppercase"
                        label="IFSC"
                        // error={helperText == 'Enter a valid IFSC'}
                        autoCapitalize="characters"
                        onBlur={() => checkOnBlurIFSC()}
                        onChange={(text) => setifsc(text.target.value)}
                    />
                    {helperType === 'ifsc' ? (
                        <span style={{ color: helperColor, fontSize: cssVar.getPropertyValue("--font-helper") }}>

                            {helperText}
                        </span>
                    ) : null}
                    <CustomDropDown
                        label="Bank name"
                        initial={banknm}
                        items={bankList}
                        onChange={(item) => setbanknm(item)} />
                </div>
            </div>
            <div className="footer">
                <BottomBackNextBtn
                    navigation={props.history}
                    next="/complete_pro/doc"
                    params={props.location.state}
                    saveData={{
                        bank: {
                            accountNo,
                            type: acctype,
                            ifsc,
                            bankName: banknm,
                            allbank: bankList
                        }
                    }}
                    nextdisabled={nextdisabled} />
            </div>
        </div>
    }
}

