import React from 'react';
import { FiArrowLeft } from "react-icons/fi";
import { HiOutlineBookmark, HiOutlineHome, HiOutlineShare, HiOutlineShoppingCart } from 'react-icons/hi';
import { IoIosHelpCircleOutline } from 'react-icons/io';
import { IoLinkOutline } from 'react-icons/io5';
import { RiPencilLine, RiShareBoxLine } from 'react-icons/ri';
import cssVar from '../../../styles';

const MainHeader = ({
    header,
    navigation,
    screenType,
    check,
    animatedValue,
    rightBtnFun,
    right,
    goBack
}) => {
    const [status, setStatus] = React.useState(false)

    React.useEffect(() => {
        window.onscroll = () => {
            var a = window.scrollY
            if (a > 0) {
                setStatus(true)
            } else {
                setStatus(false)
            }
        }
    }, []);
    const renderRightSide = () => {
        switch (right) {
            case 'cart':
                return (
                    <>
                        <IoIosHelpCircleOutline
                            className="icon-def mx-1 text-right"
                            size={24}
                        />
                        <HiOutlineShoppingCart
                            className="icon-def mx-1 text-right"
                            size={24}
                        />
                    </>
                );
            case 'help-cart':
                return (
                    <>
                        <HiOutlineHome
                            className="icon-def mx-1 text-right"
                            size={24}
                        />
                        <IoIosHelpCircleOutline
                            className="icon-def mx-1 text-right"
                            size={24}
                        />
                    </>
                );
            case 'bookmark-help-cart':
                return (
                    <>
                        <HiOutlineBookmark
                            className="icon-def mx-1 text-right"
                            size={24}
                        />
                        <IoIosHelpCircleOutline
                            className="icon-def mx-1 text-right"
                            size={24}
                        />
                        <HiOutlineShoppingCart
                            className="icon-def mx-1 text-right"
                            size={24}
                        />
                    </>
                );
            case 'link':
                return (
                    <IoLinkOutline
                        color={cssVar.getPropertyValue("--primary")}
                        className="icon-def mx-1 text-right"
                        size={24}
                    />
                );
            case 'share':
                return (
                    <HiOutlineShare
                        color={cssVar.getPropertyValue("--primary")}
                        className="icon-def mx-1 text-right"
                        size={24}
                    />
                );
            case 'upload':
                return (
                    <RiShareBoxLine
                        color={cssVar.getPropertyValue("--primary")}
                        className="icon-def mx-1 text-right"
                        size={24}
                    />
                );
            case 'edit':
                return (
                    <RiPencilLine
                        color={cssVar.getPropertyValue("--primary")}
                        className="icon-def mx-1 text-right"
                        size={24}
                    />
                );
            case 'bookmark-share':
                return (
                    <>
                        <HiOutlineBookmark
                            className="icon-def mx-1 text-right"
                            size={24}
                        />
                        <HiOutlineShare
                            className="icon-def mx-1 text-right"
                            size={24}
                        />
                    </>
                );
            case 'add-member':
                return (
                    <div className="textHelper" style={{ color: cssVar.getPropertyValue("--primary") }} onClick={() => {
                        navigation.push("/menu/add-family-member")
                    }} >
                        Add a member
                    </div>
                );
            case 'exit':
                return (<p onClick={() => navigation.push('')} >Exit</p>);
            case 'all-plans':
                return (<p onClick={() => navigation.push('')} >All plans</p>);
            default:
                return false;
        }
    };

    return (
        <div  style={{ backgroundColor: screenType === 'FundDetails' ?cssVar.getPropertyValue('--background'):cssVar.getPropertyValue('--bg')}}className={!status ? "navTop d-flex" : "navTopActive d-flex"}>
            {screenType !== 'Home' && (
                <div className=" d-flex align-items-start">
                    <FiArrowLeft
                        color={cssVar.getPropertyValue("--color-text")}
                        size={24} onClick={() => navigation.goBack()} />
                </div>

            )}
            {screenType === 'FundDetails' ? (
                <div className={screenType !== 'Home' ? "mx-2" : ""} style={{
                    overflow: "hidden", display: "flex", flex: 1,
                    textOverflow: "ellipsis", whiteSpace: 'nowrap',
                    minWidth: 0, flexDirection: "column",
                }}>
                    <span className="filename__base textHeader">{header.name}</span>
                    <span className="filename__base textHelper" style={{ color: cssVar.getPropertyValue("--textLight") }}>{header.type}</span>
                </div>
            ) : (
                <div className={screenType !== 'Home' ? "mx-2" : ""} style={{
                    overflow: "hidden", display: "flex", flex: 1,
                    textOverflow: "ellipsis", whiteSpace: 'nowrap',
                    minWidth: 0
                }}>
                    <span className="filename__base textHeader">{header}</span>
                </div>
            )}
            <div style={{ display: "flex" }}>
                {renderRightSide()}
            </div>
        </div>
    );
};

export default MainHeader;