import React, { useState } from 'react'
import './toggle.css'

export default function ToggleButton({ isChecked=false, onClick=() => {} }) {
  const [checked, setChecked] = useState(isChecked);
  
  const handleChange = (event) => {
    onClick();
    setChecked(s => !s)
  }

  return (
    <div className="toggle-container">
      <label>
        <input className="toggle" checked={checked} onChange={handleChange} 
          type="checkbox" />
        <div>
          <div></div>
        </div>
      </label>
    </div>
  )
}

