import React, { useState } from 'react';
import { Spinner } from 'react-bootstrap';
import { CurrencyFormat } from '../../../components/atoms/CurrencyFormat';
import TabBar from '../../../components/atoms/TabBar';
import FundCard from '../../../components/molecules/Cards/FundCard';
import MainHeader from '../../../components/molecules/Headers/MainHeader';
import cssVar from '../../../styles';


export default function MintboxScreen(props) {

  const [loading, setloading] = React.useState(true);
  const [mintdata, setmintdata] = useState({});
  const [mintchartData, setmintChartData] = React.useState([])

  const getMintBoxChartData = async () => {
    var aa = props.location.state.mintassetallow
    var ak = []
    aa.map((a, index) => {
      var clr = a.SchemeType === 'EQUITY' ? '#AA5F9C' : a.SchemeType === 'FOF' ? '#DCD9B1' : a.SchemeType === 'DEBT' ? '#E7AA56' : a.SchemeType === 'ELSS' ? '#5C5685' : a.SchemeType === 'CASH' ? '#5C5685' : a.SchemeType === 'GOLD' ? '#86B8B4' : '#272932'
      var dd = {
        value: Number(a.assetper).toFixed(2),
        label: a.SchemeType,
        labelColor: '#000',
        color: clr
      }
      ak.push(dd)
    })
    await setmintChartData(ak)
  }

  React.useEffect(() => {
    (async () => {
      getMintBoxChartData()
      setmintdata(JSON.parse(props.location.state.data))
      setloading(false);
    })();
  }, []);

  const ChartData = [
    { value: 25, label: 'India Equity', labelColor: '#FFF', color: '#AA5F9C' },
    { value: 25, label: 'Gold', labelColor: '#444', color: '#86B8B4' },
    { value: 20, label: 'Foreign Equity', labelColor: '#444', color: '#DCD9B1' },
    { value: 18, label: 'Debt', labelColor: '#444', color: '#E7AA56' },
    { value: 12, label: 'Cash', labelColor: '#FFF', color: '#5C5685' }
  ];

  const AssetAllocation = () => (
    <div>
      {/* <PieChart data={mintchartData} /> */}
    </div>
  );

  const Funds = () => {
    return props.location.state.funds.map((data) => {
      return (
        <div key={data.UID}>
          <FundCard
            onClick={() =>
              props.history.push({
                pathname: '/portfolio/fundScreen',
                state: { data: JSON.stringify(data) } // your data array of objects
              })
            }
            screen="Goal"
            navigation={props.history}
            data={data}
            fundData={data}
          />
        </div>
      );
    })
  }

  const Additional = () => (
    <div></div>
  );

  const MonthlySip = () => (
    <div></div>
  );

  const FundTabs = () => {

    const keyTitles = [
      { key: 'AssetAllocation', title: 'Asset allocation' },
      { key: 'Funds', title: 'Funds' },
      { key: 'MonthlySip', title: 'SIPs' },
      { key: 'Additional', title: 'Additional' }
    ];

    const keyComponents = { MonthlySip, Funds, AssetAllocation, Additional };

    return (
      <TabBar keyTitles={keyTitles} keyComponents={keyComponents} />
    );

  };

  if (loading) {
    return (
      <div className="center-screen">
        <Spinner
          as="span"
          animation="grow"
          size="sm"
          role="status"
          aria-hidden="true"
        />
      </div>
    );
  }

  return (
    <div style={{ flex: 1 }}>
      <MainHeader
        navigation={props.history}
        screenType="FundDetails"
        header={{ name: mintdata.MintboxName, type: 'Fixed asset allocation' }}
        right="upload" />
      <div style={{ flex: 1, borderRadius: 5, height: 'auto' }}>
        <div className="colCont copycontainer_side">
          <span style={{ fontSize: cssVar.getPropertyValue("--font-numHead"), }}>
            <CurrencyFormat num={mintdata.MintboxBalance} type="2" />
          </span>

          {!`${mintdata.DailyChangePerc}`.includes('-') ? (
            <span style={{ color: cssVar.getPropertyValue("--green") }}>
              <span >1 Day</span> &#x25B2; <CurrencyFormat num={mintdata.DailyChangeAmnt} /> ({`${Number(mintdata.DailyChangePerc).toFixed(2)}%`})
            </span>
          ) : (
            <span style={{ color: cssVar.getPropertyValue("--red") }}>
              &#x25BC; <CurrencyFormat num={mintdata.DailyChangeAmnt} /> (
              {`${Number(mintdata.DailyChangePerc).toFixed(3)}%`} )
            </span>
          )}
        </div>
        <div className="rowCont copycontainer_side py-2 my-2"
          style={{
            backgroundColor: cssVar.getPropertyValue("--slideCard"),
          }}>
          <div className="colCont" style={{ flex: 1 }}>
            <span style={{ textAlign: 'left' }}>
              Risk level
            </span>
            <span style={{ textAlign: 'left' }}>
              {Number(mintdata.RiskLevel) === 1 ? "Very Conservative" :
                Number(mintdata.RiskLevel) === 2 ? "Conservative" :
                  Number(mintdata.RiskLevel) === 3 ? "Moderate" :
                    Number(mintdata.RiskLevel) === 4 ? "Aggressive" : "Very Aggressive"

              }
            </span>
          </div>

          <div className="colCont" style={{ flex: 1, marginLeft: "20px" }}>
            <span style={{ textAlign: 'left' }}>
              Abs return
            </span>
            <span style={{ textAlign: 'left' }}>
              {mintdata.MintboxAbsReturn}%
            </span>
          </div>

          <div className="colCont" style={{ flex: 1 }}>
            <span style={{ textAlign: 'left' }}>
              Ann return
            </span>
            <span style={{ color: !`${mintdata.DailyChangePerc}`.includes('-') ? cssVar.getPropertyValue("--green") : cssVar.getPropertyValue("--red"), textAlign: 'left' }}>
              {`${Number(mintdata.MintboxXIRR).toFixed(2)}%`}
            </span>
          </div>
        </div>
        <FundTabs />
      </div>
    </div>
  );
}
