import React from "react";
import { Button, Spinner } from "react-bootstrap";
import { toast } from "react-toastify";
import db from "../../../../../libs/db";
import AdminServices from "../../../../../services/api/AdminServices";
import TabBar from "../../../components/atoms/TabBar";
import FundCard from "../../../components/molecules/Cards/FundCard";
import PortfolioCard from "../../../components/molecules/Cards/PortfolioCard";
import PortfolioGoalCards from "../../../components/molecules/Cards/PortfolioGoalCards";
import MainHeader from "../../../components/molecules/Headers/MainHeader";

const PortfolioScreen = (props) => {

  var portfolioData = []
  const [profiled, setProfiled] = React.useState(false);
  const [loading, setloading] = React.useState(true);
  const [selected, setselectedItem] = React.useState(1);
  const [check, setcheck] = React.useState(false);
  const [user, setUser] = React.useState({});
  const [userData, setUserData] = React.useState([]);
  const [portfolio, setportfolio] = React.useState({});
  const [refreshing, setRefreshing] = React.useState(false);
  const [toggle, settoggle] = React.useState(0);
  const [sort, setsort] = React.useState(true);


  const getData = async (user, clientData) => {
    try {
      db.get(db.model.portfolio, true).then((p) => {
        const aa = p
        if (p === null) {
          fetchData(user, clientData);
        } else {
          if (clientData[5].FamilyInfo.length === aa.length - 1) {
            setportfolio(aa);
            setloading(false);
            setRefreshing(false);
          } else {
            fetchData(user, clientData);
          }
        }
      });
    } catch (error) {
      toast.error("Try again ...");
    }
  };

  const getFamilyData = async (user, clientData) => {
    for (var i = 0; i < clientData[5].FamilyInfo.length; i++) {
      const res = await AdminServices.getPortfolio(clientData[5].FamilyInfo[i].MemberId, 0).then((r) => r);
      const aa = JSON.parse(res.data);
      var uData = {
        clientId: clientData[5].FamilyInfo[i].MemberId,
        portfolio: aa
      }
      portfolioData.push(uData)
    }
    db.set(db.model.portfolio, JSON.stringify(portfolioData), true);
    setportfolio(portfolioData);
    setloading(false);
    setRefreshing(false);
  }

  const fetchData = async (user, clientData) => {
    try {
      const res = await AdminServices.getPortfolio(user.id, 0).then((r) => r);
      const aa = JSON.parse(res.data);
      var uData = {
        clientId: user.id,
        portfolio: aa
      }
      if (portfolioData.length === 0) {
        portfolioData = [uData]
      } else {
        portfolioData.push(uData)
      }
      if (clientData[5].FamilyInfo.length > 0) {
        getFamilyData(user, clientData)
      } else {
        db.set(db.model.portfolio, JSON.stringify(portfolioData), true);
        setportfolio(portfolioData);
        setloading(false);
        setRefreshing(false);
      }
    } catch (error) {
      console.log(error)
      toast.error("Try again ...");
      setloading(false);
      setRefreshing(false);
    }
  };

  const refreshData = async (user) => {
    setRefreshing(true);
    db.remove(db.model.portfolio);
    loadData();
  };

  const getClientData = async (user) => {
    let da = await AdminServices.getClient(user.id).then((res) => {
      return res;
    });
    const aa = JSON.parse(da.data);
    db.set(db.model.appData, JSON.stringify(aa), true);
    setUserData(aa);
    getData(user, aa);
  };

  const loadData = async () => {
    try {
      db.get(db.model.user, true).then((p) => {
        setUser(p.userData);
        getClientData(p.userData);
        setProfiled(p.userData.profileComplete);
      });
    } catch (error) {
      toast.error("Try again ...");
    }
  };

  React.useEffect(() => {
    loadData();
  }, []);

  const handleScroll = (event) => {
    if (event.nativeEvent.contentOffset.y > 0) {
      setcheck(true);
    } else {
      setcheck(false);
    }
  };

  const getGoalFundsDetails = (index, data) => {
    var ak = []
    JSON.parse(portfolio[index].portfolio)[0].FundsConsolidated.map((b) =>
      b.GoalId === data.GoalId ? ak.push(b) : null
    )
    return ak
  }

  const getMintboxFundsDetails = (index, data) => {
    var ak = []
    JSON.parse(portfolio[index].portfolio)[0].FundsConsolidated.map((b) =>
      b.MintboxId === data.MintboxId ? ak.push(b) : null
    )
    return ak
  }

  const getMintboxAssetAllowDetails = (index, data) => {
    var ak = []

    JSON.parse(portfolio[index].portfolio)[9].MintboxAssetAllocation.map((b) => {
      if (b.MintboxId === data.MintboxId) {
        var per = (b.MintboxAssetBalance) / data.MintboxBalance
        per = per * 100
        b = {
          ...b,
          assetper: per
        }
        ak.push(b)
      }
    })
    return ak
  }

  const getMintboxDetails = (index, data) => {
    var ak = []
    var mintboxes = []

    if (data.Mintboxes.length === 1) {
      mintboxes.push(data.Mintboxes[0])
    } else {
      mintboxes = data.Mintboxes.split(',');
    }

    JSON.parse(portfolio[index].portfolio)[3].MintboxConsolidated.map((b) =>
      mintboxes.map((data) =>
        b.MintboxId === Number(data) ? ak.push(b) : null)
    )
    return ak
  }

  const getAssetAllowDetails = (index, data) => {
    var ak = []

    JSON.parse(portfolio[index].portfolio)[5].GoalAssetAllocation.map((b) => {
      if (b.GoalId === data.GoalId) {
        var per = (b.GoalAssetBalance) / data.GoalBalance
        per = per * 100

        b = {
          ...b,
          assetper: per
        }
        ak.push(b)
      }
    }
    )
    return ak
  }

  const getMintAssetAllowDetails = (index, data) => {

    var ak1 = []
    var mintboxes = []
    var ak = []

    if (data.Mintboxes.length === 1) {

      mintboxes.push(data.Mintboxes[0])
    } else {
      mintboxes = data.Mintboxes.split(',');
    }

    JSON.parse(portfolio[index].portfolio)[3].MintboxConsolidated.map((b) =>
      mintboxes.map((data) =>
        b.MintboxId === Number(data) ?
          JSON.parse(portfolio[index].portfolio)[9].MintboxAssetAllocation.map((a) => {
            if (a.MintboxId === b.MintboxId) {
              var per = (a.MintboxAssetBalance) / b.MintboxBalance
              per = per * 100
              a = {
                ...a,
                assetper: per
              }
              ak.push(a)
            }
          })
          : null)
    )
    return ak
  }

  const portfolioView = (index) => {
    const funds = () => (
      <div>
        {JSON.parse(portfolio[index].portfolio)[0].FundsConsolidated.sort((a, b) =>
          sort ? b.FundXIRR - a.FundXIRR : a.FundXIRR - b.FundXIRR
        ).map((data) => (
          <div key={data.UID}>
            <FundCard
              onClick={() =>
                props.history.push({
                  pathname: '/portfolio/fundScreen',
                  state: { data: JSON.stringify(data) } // your data array of objects
                })
              }
              navigation={props.history}
              data={data}
            />
          </div>
        ))}
        <div className={JSON.parse(portfolio[index].portfolio)[0].FundsConsolidated.length > 3 ? "copycontainer_side" : "footer copycontainer_side"} style={{ marginBottom: "40px" }}>
          <Button
            className="btn-normalBorder"
            onClick={() => { }
            }
          >
            Tag funds to goals
          </Button>
        </div>

      </div>
    );

    const mintbox = () => (
      <div>
        {JSON.parse(portfolio[index].portfolio)[3].MintboxConsolidated.sort((a, b) =>
          sort ? b.MintboxXIRR - a.MintboxXIRR : a.MintboxXIRR - b.MintboxXIRR
        ).map((data) => (
          <div key={data.MintboxId}>
            <FundCard
              type="mintbox"
              onClick={() =>
                props.history.push({
                  pathname: '/portfolio/mintboxScreen',
                  state: {
                    data: JSON.stringify(data),
                    funds: getMintboxFundsDetails(index, data),
                    mintassetallow: getMintboxAssetAllowDetails(index, data)
                  } // your data array of objects
                })
              }
              navigation={props.history}
              data={data}
            />
          </div>
        ))}
        <div className={JSON.parse(portfolio[index].portfolio)[3].MintboxConsolidated.length > 3 ? "copycontainer_side" : "footer copycontainer_side"} style={{ marginBottom: "40px" }}>
          <Button
            className="btn-normalBorder"
            style={{}}
            onClick={() => { }}
          >
            Tag mintboxes to goals
          </Button>
        </div>
      </div>
    );

    const goals = () => (
      <div>
        {JSON.parse(portfolio[index].portfolio)[2].GoalConsolidated.sort((a, b) =>
          sort
            ? b.GoalBalance - a.GoalBalance
            : a.GoalBalance - b.GoalBalance
        ).map((data) => (
          <div key={data.GoalId}>
            <PortfolioGoalCards
              navigation={props.history}
              data={data}
              funds={getGoalFundsDetails(index, data)}
              assetallow={getAssetAllowDetails(index, data)}
              mintbox={getMintboxDetails(index, data)}
              mintassetallow={getMintAssetAllowDetails(index, data)}

            />
          </div>
        ))}
      </div>
    );

    const keyTitles = [
      { key: "funds", title: "Funds" },
      { key: "mintbox", title: "Mintboxes" },
      { key: "goals", title: "Goals" },
    ];

    const keyComponents = { funds, mintbox, goals };

    return (
      <div className="pb-3">
        <div style={{ marginTop: "10px" }}>
          <PortfolioCard
            header={false}
            illustrationVisible
            navigation={props.history}
            data={[
              JSON.parse(portfolio[index].portfolio)[1].PortfolioConsolidated[0].CurrentBalance,
              JSON.parse(portfolio[index].portfolio)[1].PortfolioConsolidated[0].DailyChangeAmnt,
              `${JSON.parse(portfolio[index].portfolio)[1].PortfolioConsolidated[0].AbsReturn}`,
              `${JSON.parse(portfolio[index].portfolio)[1].PortfolioConsolidated[0].DailyChangePerc}`,
              JSON.parse(portfolio[index].portfolio)[1].PortfolioConsolidated[0].TotalInvestments,
              `${JSON.parse(portfolio[index].portfolio)[1].PortfolioConsolidated[0].PortfolioXIRR}`,
            ]}
          />
        </div>
        <TabBar keyTitles={keyTitles} keyComponents={keyComponents} />
      </div>
    );
  };

  const notInvest = (name, id) => (
    <Button className="btn-normal">
      {name}
    </Button>
  );

  const familyView = (userdata) => {

    const keyTitles = [
      { key: "user", title: user.name },
    ];

    const keyComponents = {
      user: () => JSON.parse(portfolio[0].portfolio)[1].PortfolioConsolidated[0] === undefined
        ? notInvest("Invest Now", 2)
        : portfolioView(0),
    };

    userData[5].FamilyInfo.map((data, index) => {
      keyTitles.push({
        key: data.MemberId,
        title: data.ClientName
      })

      var memID = data.MemberId
      var a = {
        [memID]: () => portfolio[index + 1].portfolio[1].PortfolioConsolidated[0] === undefined
          ? notInvest("Invest Now", 2)
          : portfolioView(index + 1)
      }
      Object.assign(keyComponents, a)

    })

    return (
      <TabBar keyTitles={keyTitles} keyComponents={keyComponents} />
    )

  };

  if (loading) {
    return (
      <div style={{ flex: 1 }}>
        <MainHeader
          navigation={props.history}
          header="Portfolio"
        />
        <div className="center-screen">
          <Spinner
            as="span"
            animation="grow"
            size="sm"
            role="status"
            aria-hidden="true"
          />
        </div>
      </div>
    );
  } else {
    return (
      <div style={{ flex: 1 }}>
        <MainHeader
          navigation={props.history}
          header="Portfolio"
          right="add-member"
        />
        <div className="containerData px-0">
          {user.profileComplete ? userData[5].FamilyInfo[0] === undefined
            ? JSON.parse(portfolio[0].portfolio)[1].PortfolioConsolidated[0] === undefined
              ? notInvest("Invest Now", 2)
              : portfolioView(0) : (
              <>
                {familyView(userData)}
              </>
            ) : notInvest("Complete profile", 1)}
        </div>
      </div>
    );
  }
};

export default PortfolioScreen;