import React from 'react';
import TextInput from './TextInput';

export default function InputOTP(props) {
  const { className = '', style = {}, onFilled, inputCount = 6, otpValue = () => { } } = props;

  const [otp, setOtp] = React.useState([]);

  const refs = React.useRef({});

  const focus = React.useCallback((key) => {
    if (refs.current[key]) {
      refs.current[key].focus();
    }
  }, []);

  const setRef = (name) => (input) => {
    refs.current[name] = input;
  };

  const otpVal = () => {
    otpValue(otp.join(''));
  };

  let otpCheck = [];

  const renderInputs = () => {
    const inputs = Array(inputCount).fill(0);
    const txt = inputs.map((i, j) => (
      <div key={j} className='mx-1 d-inline-block align-items-center justify-content-center'>
        <TextInput
          value={otp[j]}
          className={'' + className}
          style={{ width: 40 }}
          mode="outlined"
          keyboardType="numeric"
          maxLength={1}
          onChangeText={(t) => focusNext(j, t)}
          onKeyPress={(e) => focusPrevious(e.nativeEvent.key, j)}
          ref={setRef(j)} />
      </div>
    ));
    return txt;
  };

  const focusPrevious = async (key, index) => {
    if (key === 'Backspace' && index != 0) {
      refs.current[index - 1].focus();
      await setOtp((s) => {
        s.pop();

        return s;
      });
    }
    if (key === 'Backspace' && index == 0) {
      refs.current[index].focus();
      await setOtp([]);
    }

    otpVal();
  };

  const focusNext = async (index, num) => {
    if (index === 0) {
      // const copiedContent = await Clipboard.getString();

      // if (copiedContent.length === 6 && Number(copiedContent)) {
      //   otpCheck = await copiedContent.split('');
      //   await setOtp(copiedContent.split(''));
      //   focus(5);

      //   Clipboard.setString('');
      // } else if (num) {
      await setOtp([...otp, num]);
      focus(index + 1);
      // }
    } else if (num) {
      await setOtp([...otp, num]);
      focus(index + 1);
    }

    otpVal();
  };

  return (
    React.useEffect(() => {
      // if (otp.length == inputCount) {
      //   onFilled(otp.join(''));
      // }
      otpVal();
      // otpcheck = otp;
    }),

    React.useEffect(() => {
      // if (otp.length == inputCount) {
      //   onFilled(otp.join(''));
      // }
      // otpCheck = otp
    }, [otp]),

    (<div className='d-flex'>{renderInputs()}</div>)
  );
}
