import * as moment from 'moment';
import React, { useState } from 'react';
import { Button, Spinner } from 'react-bootstrap';
import db from '../../../../../../libs/db';
import TextInput from '../../../../components/atoms/TextInput';
import MainHeader from '../../../../components/molecules/Headers/MainHeader';
import colors from '../../../../styles';

export default function NotificationScreen(props) {
  const [query, setQuery] = useState('');
  const [notifications, setNotifications] = useState({});
  const [dataList, setDataList] = useState([]);
  const [loading, setLoading] = useState(true);

  const filterSearch = (text = '') => {
    setQuery(text);
    const dlist = notifications.all.filter(
      (item) => !item.title.toLowerCase().indexOf(text.toLowerCase())
    );
    if (text !== '') {
      setDataList(dlist);
    } else {
      setDataList(notifications.all);
    }
  };

  React.useEffect(() => {
    db.get(db.model.notification)
      .then((n) => {
        n = JSON.parse(n) || { seen: [], unseen: [] };
        n = { all: [...n.seen, ...n.unseen], ...n };
        setNotifications(n);
        setDataList(n.all);
        setLoading(false);
      })
      .catch((e) => console.log(e));
  }, []);

  const NotificationTemp = ({ date, title, text, btn }) => {
    return (
      <div>
        <div className='py-2 px-4'>
          <div className='textHelper mb-1'>{moment(date).format('Do  MMM YYYY')}</div>
          {title && <div className='textNormal b-3 mb-2'>{title}</div>}
          <div className='textNormal'>{text}</div>
          {btn && (
            <div className='pt-2 flex-fill justify-content-end'>
              <div className='d-flex'>
                <Button className='inline-block btn-normalBorder ' style={{ backgroundColor: "#FFF", borderColor: colors.primary, borderWidth: 1 }} variant="outline-primary">
                  Know more
                </Button>
                <span style={{ padding: 6 }} />
                <Button className=' inline-block btn-normal' variant="contained">
                  Approve
                </Button>
              </div>
            </div>
          )}
        </div>
        <div className='divider' />
      </div>
    );
  };



  return (
    <div style={{ flex: 1 }}>
      <MainHeader header="Notifications" navigation={props.history} />
      <div style={{ flex: 1 }}>
        <TextInput
          placeholder="Search"
          type="default"
          // mode="outlined"
          className='my-2 mx-4'
          onChange={(text) => filterSearch(text)}
          value={query} />
        <NotificationTemp
          date={new Date(2021, 0, 1)}
          text="this is a dummy one as suggest in figma and below ones are kept in db by notification "
          btn />
        {loading ? (
          <Spinner style={{ flex: 1 }} color="blue" />
        ) : (
          dataList.map((n, i) => (
            <NotificationTemp
              key={i}
              date={new Date().toUTCString()}
              title={n.title}
              text={n.message} />
          ))
        )}
      </div>

    </div>
  )
}

