import React from 'react';
import { Spinner } from 'react-bootstrap';
import { CurrencyFormat } from '../../../components/atoms/CurrencyFormat';
import { formatDate } from '../../../components/atoms/DateFormatter';
import TabBar from '../../../components/atoms/TabBar';
import FundCard from '../../../components/molecules/Cards/FundCard';
import PortfolioCard from '../../../components/molecules/Cards/PortfolioCard';
import MainHeader from '../../../components/molecules/Headers/MainHeader';

const GoalScreen = (props) => {

  const [loading, setloading] = React.useState(true);
  const [selected, setselectedItem] = React.useState(1);
  const [goalData, setGoalData] = React.useState({})
  const [sort, setsort] = React.useState(true);
  const [chartData, setChartData] = React.useState([])

  const getChartData = async () => {
    var aa = props.location.state.assetallow
    var ak = []
    aa.map((a, index) => {
      var clr = a.SchemeType === 'EQUITY' ? '#AA5F9C' : a.SchemeType === 'FOF' ? '#DCD9B1' : a.SchemeType === 'DEBT' ? '#E7AA56' : a.SchemeType === 'ELSS' ? '#5C5685' : a.SchemeType === 'CASH' ? '#5C5685' : a.SchemeType === 'GOLD' ? '#86B8B4' : '#272932'
      var dd = {
        value: Number(a.assetper).toFixed(2),
        label: a.SchemeType,
        labelColor: '#000',
        color: clr
      }
      ak.push(dd)
    })
    await setChartData(ak)
  }

  React.useEffect(() => {
    (async () => {
      getChartData()
      setGoalData(JSON.parse(props.location.state.data))
      setloading(false);
    })();

  }, []);


  const ChartData = [
    { value: 25, label: 'India Equity', labelColor: '#FFF', color: '#AA5F9C' },
    { value: 25, label: 'Gold', labelColor: '#444', color: '#86B8B4' },
    { value: 20, label: 'Foreign Equity', labelColor: '#444', color: '#DCD9B1' },
    { value: 18, label: 'Debt', labelColor: '#444', color: '#E7AA56' },
    { value: 12, label: 'Cash', labelColor: '#FFF', color: '#5C5685' }
  ];

  const getMintboxFundsDetails = (data) => {
    var ak = []

    props.location.state.funds.map((b) =>
      b.MintboxId === data.MintboxId ? ak.push(b) : null
    )
    return ak
  }

  const funds = () => {
    return props.location.state.funds.map((data) => {
      return (
        <div key={data.UID}>
          <FundCard
            onClick={() =>
              props.history.push({
                pathname: '/portfolio/fundScreen',
                state: { data: JSON.stringify(data) } // your data array of objects
              })
            }
            screen="Goal"
            navigation={props.history}
            data={data}
          />
        </div>
      );
    })
  }
  const mintbox = () => {
    return props.location.state.mintbox.map((data) => (
      <div key={data.UID}>
        <FundCard
          type="mintbox"
          onClick={() =>
            props.history.push({
              pathname: '/portfolio/mintboxScreen',
              state: {
                data: JSON.stringify(data),
                funds: getMintboxFundsDetails(data),
                mintassetallow: props.location.state.mintassetallow
              } // your data array of objects
            })
          }
          navigation={props.history}
          data={data}
        />
      </div>
    ))

  }

  const assetAllocation = () => (
    <div>
      {/* <PieChart data={chartData} /> */}
    </div>
  );

  const keyTitles = [
    { key: "assetAllocation", title: "Asset allocation" },
    { key: "funds", title: "Funds" },
    { key: "mintbox", title: "Mintboxes" },
  ];

  const keyComponents = { funds, mintbox, assetAllocation };

  if (loading) {
    return (
      <div className="center-screen">
        <Spinner
          as="span"
          animation="grow"
          size="sm"
          role="status"
          aria-hidden="true"
        />
      </div>
    );
  } else {
    return (
      <div style={{ flex: 1 }}>
        <MainHeader
          navigation={props.history}
          screenType="FundDetails"
          header={{
            name: goalData.GoalName,
            type: `Target ${CurrencyFormat({ type: '2', num: goalData.GoalTargetCorpus })} | Target date ${formatDate(goalData.GoalTargetDate)}`
          }}
          right="edit"
        />
        <div className="pb-5">
          <PortfolioCard
            header="Goal value"
            type="goal"
            illustrationVisible={true}
            navigation={props.history}
            data={[
              JSON.parse(props.location.state.data).GoalBalance,
              JSON.parse(props.location.state.data).DailyChangeAmnt || 0,
              JSON.parse(props.location.state.data).GoalAbsReturn + '' || '0',
              JSON.parse(props.location.state.data).DailyChangePerc + '' || '0',
              JSON.parse(props.location.state.data).PercAchieved + '' || '0',
              JSON.parse(props.location.state.data).GoalXIRR + '' || '0'
            ]}
          />
          <TabBar
            keyTitles={keyTitles}
            keyComponents={keyComponents}
          />
        </div>
      </div>
    );
  }
};

export default GoalScreen;
