import { useEffect, useState } from 'react'
import { FiBriefcase } from 'react-icons/fi'
import { HiOutlineCurrencyRupee, HiOutlineHome, HiOutlineMenu } from 'react-icons/hi'
import { IoGiftOutline } from 'react-icons/io5'
import { Link, useHistory, useLocation } from 'react-router-dom'
import colorsFinal from '../../../../theme/colors'

const BottomNavBar = props => {
    const history = useHistory()
    const location = useLocation();
    const [activeTabs, setActiveTabs] = useState('home')
    
    useEffect(() => {
        if (location.pathname.includes("dashboard")) {
            setActiveTabs('home')
        } else if (location.pathname.includes("portfolio")) {
            setActiveTabs('portfolio')
        } else if (location.pathname.includes("invest")) {
            setActiveTabs('invest')
        } else if (location.pathname.includes("rewards")) {
            setActiveTabs('rewards')
        } else if (location.pathname.includes("menu")) {
            setActiveTabs('menu')
        }
    })

    return (
        <div className='bottom-nav'>
            <div className='bn-tab'>
                <Link style={{ textDecoration: 'none' }} to="/dashboard"
                    onClick={() => setActiveTabs('home')}>
                    <HiOutlineHome
                        size='20'
                        color={activeTabs !== 'home' ? '#999999' : colorsFinal.brandPrimary}
                        onClick={() => setActiveTabs('home')}
                    />
                    <p className="textNav" style={{ color: activeTabs !== 'home' ? '#999999' : colorsFinal.brandPrimary }}
                    >Home</p>
                </Link>
            </div>
            <div className='bn-tab'>
                <Link style={{ textDecoration: 'none' }} to="/portfolio"
                    onClick={() => setActiveTabs('portfolio')}>
                    <FiBriefcase
                        size='20'
                        color={activeTabs !== 'portfolio' ? '#999999' : colorsFinal.brandPrimary}
                        onClick={() => setActiveTabs('portfolio')}
                    />
                    <p className="textNav" style={{ color: activeTabs !== 'portfolio' ? '#999999' : colorsFinal.brandPrimary }}
                    >Portfolio</p>
                </Link>
            </div>
            <div className='bn-tab'>
                <Link style={{ textDecoration: 'none' }} to="/invest"
                    onClick={() => setActiveTabs('invest')}>
                    <HiOutlineCurrencyRupee
                        size='20'
                        color={activeTabs !== 'invest' ? '#999999' : colorsFinal.brandPrimary}
                        onClick={() => setActiveTabs('invest')}
                    />
                    <p className="textNav" style={{ color: activeTabs !== 'invest' ? '#999999' : colorsFinal.brandPrimary }}
                    >Invest</p>
                </Link>
            </div>
            <div className='bn-tab'>
                <Link style={{ textDecoration: 'none' }} to="/menu/rewards"
                    onClick={() => setActiveTabs('rewards')}>
                    <IoGiftOutline
                        size='20'
                        color={activeTabs !== 'rewards' ? '#999999' : colorsFinal.brandPrimary}
                        onClick={() => setActiveTabs('rewards')}
                    />
                    <p className="textNav" style={{ color: activeTabs !== 'rewards' ? '#999999' : colorsFinal.brandPrimary }}
                    >Rewards</p>
                </Link>
            </div>
            <div className='bn-tab'>
                <Link style={{ textDecoration: 'none' }} to="/menu"
                    onClick={() => setActiveTabs('menu')}>
                    <HiOutlineMenu
                        size='20'
                        color={activeTabs !== 'menu' ? '#999999' : colorsFinal.brandPrimary}
                        onClick={() => setActiveTabs('menu')}
                    />
                    <p className="textNav" style={{ color: activeTabs !== 'menu' ? '#999999' : colorsFinal.brandPrimary }}
                    >Menu</p>
                </Link>
            </div>
        </div>
    )
}

export default BottomNavBar