import React from 'react';
import { FiArrowLeft } from "react-icons/fi";
import cssVar from 'views/mobileView/styles';
import minbox from "../../../../../assets/SVG/mintbox_logo.svg";
const AuthHeader = ({
  header,
  navigation,
  screenType,
  check,
  animatedValue,
  rightBtnFun,
  right = '',
  goBack,
  props
}) => {

  const renderRightSide = () => {
    switch (right) {
      case 'logout':
        return (<span onClick={() => {
          localStorage.clear();
          navigation.push("/email")
        }}
          style={{ color: cssVar.getPropertyValue('--primary') }}>Logout</span>);
      default:
        return <div style={{ padding: 5 }}></div>;
    }
  };

  return (
    <div className="d-flex flex-row justify-content-between pt-2 ">
      {screenType === 'Home' ? null : (
        <FiArrowLeft size={25} color={cssVar.getPropertyValue("--primary")}
          onClick={() => navigation.goBack()} />
      )}
      <div style={{ flex: 1 }} className="d-flex justify-content-center align-items-center">
        <img alt="" src={minbox} height="23px" width="100%" />
      </div>
      {screenType === 'Home' ? null : (
        <div
          style={{
            marginRight: 25
          }}>
        </div>
      )}
      <div>
        {renderRightSide()}
      </div>
    </div>
  );
};

export default AuthHeader;
