import DataFrame from 'libs/DataFrame';
import React, { useEffect, useRef } from 'react';
import * as d3 from 'd3';

const GRAPH_MARGIN = 20;
const SVGHeight = 300;
const SVGWidth = window.innerWidth - window.innerWidth % 10;
const height = SVGHeight - 2 * GRAPH_MARGIN;
const width = SVGWidth - 2 * GRAPH_MARGIN;
const labelArea = 15;

export default function BarChart(props) {
  const {data=Data} = props;
  const chart = useRef();

  const xDomain = data.map(d => d.name)
  const yDomain = [0, d3.max(data.map(d => d.value))];

  const xRange = [labelArea, width - labelArea];
  const yRange = [height - labelArea, labelArea];

  const x = d3.scaleBand().range(xRange).domain(xDomain).padding(0.2);
  const y = d3.scaleLinear().domain(yDomain).range(yRange);

  const make = () => {
    // append the svg object to the body of the page
    const svg = d3.select(chart.current)
                  .attr("width", width + labelArea)
                  .attr("height", height + labelArea)
                  .append("g")
                  .attr("transform", `translate(${labelArea}, 0)`);
    
    const x_axis = d3.axisBottom().scale(x);
    const y_axis = d3.axisLeft().scale(y);;

    svg.append('g')
        .attr("transform", `translate(0, ${height - labelArea})`)
        .call(x_axis)
          .attr('class', 'x-axis')
          .selectAll("text")
            .attr("transform", "translate(-10,0)rotate(-25)")
            .style("text-anchor", "middle");
    
    svg.append('g').attr("transform", `translate(${labelArea}, 0)`).call(y_axis).attr('class', 'y-axis');

    svg.selectAll("mybar")
      .data(data)
      .join("rect")
        .attr("x", d => x(d.name))
        .attr("y", d => y(d.value))
      .attr("width", x.bandwidth())
      .attr("height", d => height - labelArea - y(d.value))
      .attr("fill", "#69b3a2")
  }

  useEffect(() => {
    make()
  }, [])

  return (
    <div id='bar-chart'>
      <svg ref={chart} />
    </div>
  )
}

const Data = [
  {name: "A", value: 100},
  {name: "B", value: 45},
  {name: "C", value: 50},
  {name: "D", value: 20},
  {name: "E", value: 70},
  {name: "F", value: 50},
  {name: "G", value: 40},
  {name: "H", value: 10},
]