import React from 'react';
import { Modal, Spinner } from 'react-bootstrap';
import FloatingTextInput from 'views/mobileView/components/atoms/FloatingTextInput';
import Footer from '../Footer';
import Header from '../Header';
import { Chip } from '@mui/material';
import db from '../../../../../../libs/db';


export default function CapabilityPage2(props) {

	const chipset = [
		{ "key": "0", "name": "No dependents" },
		{ "key": "1", "name": "1 dependent" },
		{ "key": "2", "name": "2 dependents" }
	]
	const [loading, setloading] = React.useState(true);
	const [nextdisabled, setnextDisabled] = React.useState(true);

	const [dependents,setDependents]=React.useState("");
	
	const checkValidation = () => {
		if(dependents){
			setnextDisabled(false);
		} else {
			setnextDisabled(true)
		}
	};

	React.useEffect(() => {
		checkValidation();
	})

	React.useEffect(() => {
		db.get(db.model.userData).then((u) => {
				console.log(u)
				if (u) {
						if (u.riskCapability) {
							setDependents(u.riskCapability.dependents || '')
						}
				}
				setloading(false);
		});
		checkValidation();
	}, []);

	if (loading) {
		return (
			<div className="container">
				<Spinner
					as="span"
					animation="grow"
					size="sm"
					role="status"
					aria-hidden="true"
				/>
			</div>
		);
	} else {
		return <div className="container">
			<Header headerText="Risk-taking capability" helperText="2/6 questions" navigation={props.history} />

			<div className="mt-4 mb-2">
				<div className="textHead">How many financial dependents do you have?</div>
				<FloatingTextInput
						type="text"
						label="Dependents"
						id="dependents"
						value={dependents}
						onChange={(num) => {
								const a = num.target.value.replace(/\D/g, '')
								setDependents(a);
						}}
				/>
				<div style={{ display: 'flex', flexDirection: 'row', justifyContent: 'space-between' , overflowX: 'auto'}}>
					{chipset.map((item) => {
						return (
							<Chip
								className="chip-a"
								label={item.name}
								onClick={() => {
									setDependents(item.name)
								}}
							/>
						)
					})}
				</div>
				<div className="footer">
					<Footer
						navigation={props.history}
						next="/find-mintbox/risk-taking-capability/3"
						params={props.location.state}
						saveData={{
							riskCapability: {
								dependents,
							}
						}}
						nextdisabled={nextdisabled} 
						buttonText="Next &#8594;"/>
				</div>
			</div>
		</div>
	}
}
