import React from 'react';
import { Button, Modal, Spinner } from 'react-bootstrap';
import Draggable from 'react-draggable';
import { MdPictureInPictureAlt } from 'react-icons/md';
import YouTube from 'react-youtube';
import Footer from './Footer';
import Header from './Header';
import cssVar from "views/mobileView/styles";

export default function GetStarted(props) {

	const [loading, setloading] = React.useState(true);
	const [nextdisabled,setnextDisabled] = React.useState(true);
	const [isVideoPlaying, setVideoPlaying] = React.useState(false);
	const videoID = "i-RWBHjMiFQ"

	React.useEffect(() => {
		setloading(false);
		setnextDisabled(false);
	})

	if (loading) {
		return (
			<div className="container">
				<Spinner
					as="span"
					animation="grow"
					size="sm"
					role="status"
					aria-hidden="true"
				/>
			</div>
		);
	} else {
		return <div className="container">
			<Header headerText="Find Your Perfect Mintbox" helperText="" navigation={props.history} />
			{isVideoPlaying?
				<Draggable axis="y" defaultPosition={{x: 0,y:50}} allowAnyClick={true} 
					bounds={{top:50,bottom:window.innerHeight-280}}> 
					<div style={{  height:"200px" ,width: "90%", backgroundColor: "#000",padding:"5% 0",position:"fixed",zIndex:1}}>
						<YouTube videoId={videoID}
							onReady={(event)=>{event.target.playVideo()}}
							opts={{
								height: '100%',
								width: '100%',
								playerVars: {
									autoplay: 1,
									controls: 0
								},
							}}
						/>
					</div>
				</Draggable>
			: null}
			<div className="mt-4 mb-2">
				<div className="textHead">Understand the process in 45 sec</div>
				<div className="textCardHelper">Recommended but optional to watch</div>
				<div style={{ marginTop: "18px" }}>
					<div style={{ borderRadius: 10}}>
						<div style={{ position: "relative" }} onClick={()=>{setVideoPlaying(!isVideoPlaying)}}>
							<img
								src={`https://img.youtube.com/vi/${videoID}/maxresdefault.jpg`}
								style={{
									paddingVertical: 10,
									width: "100%",
									borderRadius: 10,
									filter: isVideoPlaying ? 'blur(5px)' : 0,
								}} />

							<img
								src='https://cdn4.iconfinder.com/data/icons/logos-and-brands/512/395_Youtube_logo-512.png'
								style={{
									position: 'absolute',
									width: "40px",
									bottom: 8,
									right: 16,
									filter: isVideoPlaying ? 'blur(1px)' : 0,
								}} />
							{isVideoPlaying ?
								<div style={{ width: "100%", textAlign: "center", position: "absolute", top: "40%" }}>
									<MdPictureInPictureAlt style={{ paddingHorizontal: 10, color: cssVar.getPropertyValue("--primary") }} size={24} />
									<p style={{ color: "#fff", }}>This video is playing in picture-in-picture</p>
								</div>
								: null}

						</div>

					</div>
				</div>
				<div className="footer">
					<Footer
						navigation={props.history}
						next="/find-mintbox/risk-taking-capability/1"
						params={props.location.state}
						nextdisabled={nextdisabled} 
						buttonText="Get started!"/>
				</div>
			</div>
		</div>
	}
}
