import React, { useState } from 'react';
import { Button } from 'react-bootstrap';
import InputOTP from 'views/mobileView/components/atoms/InputOTP';
import MainHeader from 'views/mobileView/components/molecules/Headers/MainHeader';

export default function ChangePINScreen(props) {
  const [newPIN, setNewPIN] = useState('');
  const [cPIN, setCPIN] = useState('');

  const validateInput = () => {
    if (newPIN !== '') {
      return newPIN === cPIN;
    }
    return false;
  };

  return (
    <div className='flex-fill'>
      <MainHeader header='Change PIN' navigation={props.history} />
      <div className='container'>
        <div className='my-5'>
          <span className='mx-2 textHead'>Enter new PIN</span>
          <InputOTP
            inputCount={6}
            onFilled={(otp) => {
              setNewPIN(otp);
            }} className='mt-5' />
        </div>
        <div className='my-5'>
          <span className='mx-3 textHead'>Confirm PIN</span>
          <InputOTP
            inputCount={6}
            onFilled={(otp) => {
              setCPIN(otp);
            }}
            className='mt-5' />
        </div>
        <div style={{ flex: 1, justifyContent: 'flex-end' }}>
          <Button
            className='btn-normal m-2'
            style={{ opacity: validateInput() ? 1 : 0.4 }}
            variant="primary">
            Update PIN
          </Button>
        </div>
      </div>
    </div>
  )
}
