import React from 'react';
import cssVar from '../../../styles';
import { CurrencyFormat } from '../../atoms/CurrencyFormat';

const PortfolioCard = (props) => {
  const { header, navigation, illustrationVisible, data, type, duration = '1 Day' } = props;

  const combinedView = () => (
    <div className="rowCont copycontainer_side py-4 my-2"
      style={{
        backgroundColor: cssVar.getPropertyValue("--slideCard"),
      }}>
      <div className="colCont">
        <span style={{ textAlign: 'left', color: cssVar.getPropertyValue("--textLight") }}>
          Invested
        </span>
        <span style={{ textAlign: 'left' }}>
          <CurrencyFormat num={data[4]} type="2" />
        </span>
      </div>
      <div className="colCont">
        <span style={{ textAlign: 'left', color: cssVar.getPropertyValue("--textLight") }}>
          Abs.Return
        </span>
        <span style={{ color: !data[2].includes('-') ? cssVar.getPropertyValue("--green") : cssVar.getPropertyValue("--red"), textAlign: 'left' }}>
          {`${Number(data[2]).toFixed(2)}%`}
        </span>
      </div>

      <div className="colCont">
        <span style={{ textAlign: 'left', color: cssVar.getPropertyValue("--textLight") }}>
          Ann.Return
        </span>
        <span style={{ color: !data[5].includes('-') ? cssVar.getPropertyValue("--green") : cssVar.getPropertyValue("--red"), textAlign: 'left' }}>
          {`${Number(data[5]).toFixed(2)}%`}
        </span>
      </div>
    </div>
  );

  const goalView = () => (
    <div className="rowCont copycontainer_side py-4 my-2"
      style={{
        backgroundColor: cssVar.getPropertyValue("--slideCard"),
      }}>
      <div className="colCont">
        <span style={{ textAlign: 'left', color: cssVar.getPropertyValue("--textLight") }}>
          Goal achieved
        </span>
        <span style={{ textAlign: 'left' }}>
          {`${Number(data[4]).toFixed(2)}%`}
        </span>
      </div>
      <div className="colCont">
        <span style={{ textAlign: 'left', color: cssVar.getPropertyValue("--textLight") }}>
          Abs.Return
        </span>
        <span style={{ color: !data[2].includes('-') ? cssVar.getPropertyValue("--green") : cssVar.getPropertyValue("--red"), textAlign: 'left' }}>
          {`${Number(data[2]).toFixed(2)}%`}
        </span>
      </div>

      <div className="colCont">
        <span style={{ textAlign: 'left', color: cssVar.getPropertyValue("--textLight") }}>
          Ann.Return
        </span>
        <span style={{ color: !data[5].includes('-') ? cssVar.getPropertyValue("--green") : cssVar.getPropertyValue("--red"), textAlign: 'left' }}>
          {`${Number(data[5]).toFixed(2)}%`}
        </span>
      </div>
    </div>
  );

  const fundView = () => (
    <div className="rowCont copycontainer_side py-4 my-2"
      style={{
        backgroundColor: cssVar.getPropertyValue("--slideCard"),
      }}>
      <div className="colCont" style={{ flex: 1 }}>
        <span style={{ textAlign: 'left', color: cssVar.getPropertyValue("--textLight") }}>
          Avg NAV
        </span>
        <span style={{ textAlign: 'left' }}>
          {data[4]}
        </span>
      </div>

      <div className="colCont" style={{ flex: 1 }}>
        <span style={{ textAlign: 'left', color: cssVar.getPropertyValue("--textLight") }}>
          No. of units
        </span>
        <span style={{ textAlign: 'left' }}>
          {data[2]}
        </span>
      </div>

      <div className="colCont" style={{ flex: 1 }}>
        <span style={{ textAlign: 'left', color: cssVar.getPropertyValue("--textLight") }}>
          Ann.Return
        </span>
        <span style={{ color: !data[5].includes('-') ? cssVar.getPropertyValue("--green") : cssVar.getPropertyValue("--red"), textAlign: 'left' }}>
          {`${Number(data[5]).toFixed(2)}%`}
        </span>
      </div>
    </div>
  );

  return (
    <div >
      <div style={{ flex: 1, borderRadius: 5, height: 'auto' }}>
        <div className="colCont copycontainer_side">
          {header && (
            <span style={{ fontFamily: 'DMSans-Regular' }}>
            </span>
          )}

          <span style={{ fontSize: cssVar.getPropertyValue("--font-numHead"), }}>
            <CurrencyFormat num={data[0]} type="2" />
          </span>

          {!data[3].includes('-') ? (
            <span style={{ color: cssVar.getPropertyValue("--green") }}  >
              <span >{duration}</span> &#x25B2; <CurrencyFormat num={data[1]} /> ({`${Number(data[3]).toFixed(2)}%`})
            </span>
          ) : (
            <span style={{ color: cssVar.getPropertyValue("--red") }}  >
              &#x25BC; <CurrencyFormat num={data[1]} /> (
              {`${Number(data[3]).toFixed(2)}%`} )
            </span>
          )}
        </div>

        {type === 'fund' ? fundView() : type === "goal" ? goalView() : combinedView()}
      </div>
    </div>
  );
};

export default PortfolioCard;
