import React from 'react';

export default function TextInput({ style={}, className, type='email', placeholder='', value='', label, onChange = () => {} }) {
  return (
    <div className={"floating-form " + className} style={style}>
      <div className="floating-label">
        <input onChange={onChange} value={value} className="floating-input" type={type} placeholder={placeholder} />
        <span className="highlight"></span>
        <label className="floating-label-input" >{label}</label>
      </div>
    </div>
  )
}