import React, { useEffect, useRef } from 'react'
import * as d3 from 'd3';

const GRAPH_MARGIN = 0;
const SVGHeight = 300;
const SVGWidth = window.innerWidth - window.innerWidth % 10;
const Height = SVGHeight - 2 * GRAPH_MARGIN;
const Width = SVGWidth - 2 * GRAPH_MARGIN;
const paddingArea = 0;

const Data = [
  { value: 10, label: 'India Equity', labelColor: '#FFF', color: '#013565' },
  { value: 22, label: 'Gold', labelColor: '#444', color: '#F9CD22' },
  { value: 31, label: 'Foreign Equity', labelColor: '#FFF', color: '#E4332D' },
  { value: 12, label: 'Debt', labelColor: '#FFF', color: '#009AC0' },
  { value: 22, label: 'Cash', labelColor: '#444', color: '#52D29F' }
];

const alignChartVerticaly = (align, height) => {
  switch (align) {
  case 'top':
    return -height / 2.75;
  case 'center':
    return -height / 2;
  case 'bottom':
    return -height / 1.5;
  default:
    return -height / 2;
  }
};

export default function PieChart(props) {
  const {
    width = Width, height = Height, data=Data, radius = Math.min(width, height) / 2.75, style
  } = props;
  const chart = useRef()

  const pie = d3.pie().sort(null).value((d) => d.value);

  const arc = d3.arc().innerRadius(0).outerRadius(radius);

  const colorRange = d3.quantize((t) => d3.interpolateSpectral(t * 0.8 + 0.1), data.length).reverse()

  const color = d3.scaleOrdinal().domain(data.map(d => d.color)).range(colorRange);

  const arcLabel = d3.arc().innerRadius(-(radius / 10)).outerRadius(radius*1.5);

  const arcs = pie(data);

  const makePie = () => {
    const svg = d3.select(chart.current)
                .attr("width", width)
                .attr("height", height)
                .append("g")
                .attr("transform", `translate(${width/3}, ${height/2})`);

    svg.selectAll('pie-arc')
      .data(arcs)
      .join('path')
      .attr('d', arc)
    .attr('fill', (d) => d.data.color )
    .attr("stroke", "#Fafafa")
    .style("stroke-width", "1px")
    .style("opacity", 1)

    svg.append("g")
      .attr("font-family", "sans-serif")
      .attr("font-size", 12)
      .attr("text-anchor", "middle")
      .selectAll("text")
      .data(arcs)
      .join("text")
        .attr("transform", d => `translate(${arcLabel.centroid(d)})`)
        // .call(text => text.append("tspan")
        //                   .attr("y", "-0.5em")
        //                   .style("font-size", 12)
        //                   .style("fill", d => d.data.labelColor)
        //                   .text(d => d.data.label)
        // )
        .call(text => text.filter(d => (d.endAngle - d.startAngle) > 0.25)
                          .append("tspan")
                          .attr("x", 0)
                          .attr("y", "1em")
                          .style("font-size", 12)
                          .style("fill", d => d.data.labelColor)
                          .text(d => `${(((d.endAngle - d.startAngle) / (2 * Math.PI)) * 100).toFixed(2)}%`)
        )
    
    svg.append('g')
      .attr("transform", d => `translate(${radius + 20}, ${-radius / 1.8})`)
      .attr("text-anchor", "middle")
      .selectAll('circle.label-cirlce') 
      .data(Data).enter()
      .append('circle')
      .each(function(z, i) {
        d3.select(this)
          .attr("r", '5px')
          .attr("cy", `${10 * i * 2.5}px`)
          .style("fill", d => z.color)
      })
      
      svg.append('g')
        .attr("transform", d => `translate(${radius + 25}, ${-radius / 1.8})`)
        .attr("text-anchor", "center")
        .selectAll("text")
        .data(Data).enter()
        .append("text")
        .each(function(z, i) {
          d3.select(this)
            .attr("x", "1em")
            .attr("y", `${10 * i * 2.5}px`)
            .style("font-size", 12)
            .style("fill", d => '#444')
            .text(d => z.label)
        })

  }

  useEffect(() => {
    makePie()
  }, [])

  return ( 
    <div style={{...style}} className='mx-auto'>
      <svg ref={chart} />
    </div>
  )
}