import React, { useEffect, useRef } from 'react'
import * as d3 from 'd3';
import DataFrame from 'libs/DataFrame';

const GRAPH_MARGIN = 20;
const SVGHeight = 300;
const SVGWidth = window.innerWidth - window.innerWidth % 10;
const height = SVGHeight - 2 * GRAPH_MARGIN;
const width = SVGWidth - 2 * GRAPH_MARGIN;
const labelArea = 15;

const data2 = [
  {x: 0, y: 0},
  {x: 1, y: 12},
  {x: 2, y: 22},
  {x: 3, y: 33},
  {x: 4, y: 4},
  {x: 5, y: 15},
];

export default function LineCharButton(props) {
  let {data=[]} = props;

  const chart = useRef();

  data = [
    {x: 0, y: 0},
    {x: 1, y: 1},
    {x: 2, y: 2},
    {x: 3, y: 3},
    {x: 4, y: 4},
    {x: 5, y: 5},
  ];

  const make = (data) => {
    const df = new DataFrame({data});
    const xDomain = [df.minXs(), df.maxXs()];
    const yDomain = [df.minYs(), df.maxYs()];

    const xRange = [labelArea, width - labelArea];
    const yRange = [height - labelArea, labelArea];

    const x = d3.scaleLinear().domain(xDomain).range(xRange);
    const y = d3.scaleLinear().domain(yDomain).range(yRange);

    const svg = d3.select(chart.current)
                  .attr("width", width + labelArea)
                  .attr("height", height + labelArea)
                  .append("g")
                  .attr("transform", "translate(" + labelArea + "," + 0 + ")");
    
    const x_axis = d3.axisBottom().scale(x);
    const y_axis = d3.axisLeft().scale(y);;

    svg.append('g').attr("transform", `translate(0, ${height - labelArea})`).call(x_axis).attr('class', 'x-axis');
    svg.append('g').attr("transform", `translate(${labelArea}, 0)`).call(y_axis).attr('class', 'y-axis');

    const bisect = () => {
      const bisects = d3.bisector(d => d.x).left;
      return mx => {
        const xv = x.invert(mx);
        const index = bisects(data, xv, 1);
        const a = data[index - 1];
        const b = data[index];
        return b && (xv - a.x > b.x - xv) ? b : a;
      };
    }

    const callout = (g, value) => {
      if (!value) return g.style("display", "none");
    
      g.style("display", null)
        .style("pointer-events", "none")
        .style("font", "10px sans-serif");
    
      const path = g.selectAll("path")
                    .data([null])
                    .join("path")
                      .attr("fill", 'red')
                      .attr("stroke", "black");
    
      const text = g.selectAll("text")
                    .data([null])
                    .join("text")
                    .call(text => text
                      .selectAll("tspan")
                      .data((value + "").split(/\n/))
                      .join("tspan")
                        .attr("x", 0)
                        .attr("y", (d, i) => `${i * 1.1}em`)
                        .style("font-weight", (_, i) => i ? null : "bold")
                        .text(d => d));
    
      const {x, y, width: w, height: h} = text.node().getBBox();
    
      text.attr("transform", `translate(${-w / 2},${15 - y})`);
      path.attr("d", `M${-w / 2 - 10},5H-5l5,-5l5,5H${w / 2 + 10}v${h + 20}h-${w + 20}z`);
    }

    const formatValue = (value) =>  value.toLocaleString("en", { style: "currency", currency: "USD" });

    const formatDate = (date) => {
      return date.toLocaleString("en", {
        month: "short", day: "numeric", year: "numeric", timeZone: "UTC"
      });
    }

    const tooltip = svg.append("g");

    svg.on("touchmove mousemove", (event) => {
      const {date, value} = bisect(d3.pointer(event, this)[0]);

      tooltip.attr("transform", `translate(${x(date)},${y(value)})`)
          .call(callout, `${formatValue(value)} \n ${formatDate(date)}`);
    });

    svg.on("touchend mouseleave", () => tooltip.call(callout, null));

  }

  const update = (data) => {
    const df = new DataFrame({data});
    const xDomain = [df.minXs(), df.maxXs()];
    const yDomain = [df.minYs(), df.maxYs()];

    const xRange = [labelArea, width - labelArea];
    const yRange = [height - labelArea, labelArea];

    const x = d3.scaleLinear().domain(xDomain).range(xRange);
    const y = d3.scaleLinear().domain(yDomain).range(yRange);
    
    const svg = d3.select(chart.current)
                .attr("width", width + labelArea)
                .attr("height", height + labelArea).select('g')
                .attr("transform", "translate(" + labelArea + "," + 0 + ")");

    const line = d3.line().x(d => x(d.x)).y(d => y(d.y)).curve(d3.curveBasis)(data);

    const x_axis = d3.axisBottom().scale(x);
    const y_axis = d3.axisLeft().scale(y);;
 
    svg.selectAll(".x-axis").transition().duration(1000).call(x_axis)
    svg.selectAll(".y-axis").transition().duration(1000).call(y_axis)
  
    svg.selectAll(".line")
      .data(data)
      .join("path")
      .attr("class", "line")
      .transition()
      .duration(1000)
      .attr("d", line)
        .attr("fill", "none")
        .attr("stroke", "steelblue")
        .attr("stroke-width", 2)
  }
  

  useEffect(() => {
    make(data);
    update(data)
  }, [])

  return (
    <>
      <div id='line-chart'>
        <svg ref={chart} />
      </div>
      <div style={{ display: 'flex', flexDirection: 'row', justifyContent: 'space-between' }}>
        <button style={{ flex: 1 }} className='mx-1 multiline-chart-btn'  onClick={() => update(data)}>Dataset 1</button>
        <button style={{ flex: 1 }} className='mx-1 multiline-chart-btn'  onClick={() => update(data2)}>Dataset 2</button>
      </div>
    </>
  )
}
