import db from '../db';

export default class Schema {
  createTable() {
    let query = `CREATE TABLE IF NOT EXISTS ${this.name} (`;
    const columns = Object.keys(this.cols);
    columns.forEach((k, i) => query += `${k} ${this.cols[k]}${columns.length - 1 === i ? '' : ', '}`);
    query += ');';
    db.run(query).then((d) => console.log(d)).catch((e) => console.log(e));
    return query;
  }

  async get(limit = false, filter = false) {
    let query = `SELECT * FROM ${this.name} `;
    if (filter) query += `WHERE ${filter}`;
    if (limit) query += `LIMIT ${limit}`;
    query += ';';
    const z = await db.run(query);
    return z.rows.raw();
  }

  async update(dic, filter) {
    let query = `UPDATE ${this.name} SET `;
    Object.entries(dic).forEach((d, i) => query += `${d[0]} = ${d[1]}${Object.entries(dic).length - 1 === i ? '' : ', '}`);
    query += `WHERE ${filter};`;
    return query;
  }

  async delete(filter = false) {
    if (filter) {
      const query = `DELETE FROM ${this.name} WHERE ${filter}`;
      return await db.run(query);
    }
    console.error("Filter can't be null you have to pass it.");
    return new Error("Filter can't be null you have to pass it.");
  }

  async insert(values) {
    const columns = Object.keys(this.cols);
    const partBy = 10;
    let s = 0;
    let e = partBy;
    const partinoed = [];
    const groups = Array.from({ length: values.length / partBy }, () => 0);
    groups.forEach((v, i) => {
      partinoed.push(values.slice(s, e));
      s = e;
      e += partBy;
    });
    partinoed.forEach((part) => {
      let query = `INSERT INTO ${this.name} (${columns.join(', ')}) VALUES `;
      part.forEach((vals, i) => {
        let obj = '(';
        columns.forEach((z, j) => {
          obj += `${this.cols[columns[j]].startsWith('VARCHAR') ? `'${vals[z]}'` : `${vals[z]}`}${Object.values(vals).length - 1 === j ? '' : ', '}`;
        });
        obj += `)${part.length - 1 === i ? '' : ', '}`;
        query += obj;
      });
      query += ';';
      db.run(query)
        .then((r) => console.log(r))
        .catch((e) => console.log(e));
    });
    return 'ok';
  }
}
