import React from 'react';
import { Spinner } from 'react-bootstrap';
import Footer from '../Footer';
import Header from '../Header';
import db from '../../../../../../libs/db';

export default function WillingnessPage1(props) {
	const RadioInputOptions = [
		{ key: '0', name: 'A portfolio that can yield returns anywhere between -10% and 30%' },
		{ key: '1', name: 'A portfolio that can yield returns anywhere between -5% and 20%' },
		{ key: '2', name: 'A portfolio that can yield returns anywhere between 0% and 10%' },
]
	const [loading, setloading] = React.useState(true);
	const [nextdisabled, setnextDisabled] = React.useState(true);

	const [willingnessQuestion1,setWillingnessQuestion1]=React.useState("");

	const checkValidation = () => {
		if(willingnessQuestion1){
			setnextDisabled(false);
		} else {
			setnextDisabled(true)
		}
	};

	React.useEffect(() => {
		checkValidation();
	})

	React.useEffect(() => {
		db.get(db.model.userData).then((u) => {
				console.log(u)
				if (u) {
						if (u.riskCapability) {
							setWillingnessQuestion1(u.riskCapability.willingnessQuestion1 || '')
						}
				}
				setloading(false);
		});
		checkValidation();
	}, []);

	if (loading) {
		return (
			<div className="container">
				<Spinner
					as="span"
					animation="grow"
					size="sm"
					role="status"
					aria-hidden="true"
				/>
			</div>
		);
	} else {
		return <div className="container">
			<Header headerText="Risk-taking willingness" helperText="1/2 questions" navigation={props.history} />
			<div className="mt-4 mb-2">
				<div className="textHead" style={{marginBottom:'4%'}}>Which of the following portfolios would you prefer to invest in?</div>
				{RadioInputOptions.map((obj) => (
                    <div key={obj.key} style={{ display: 'flex', flexDirection: 'row' }}>
                        <div className="radio-item">
                            <input
                                type="radio"
                                id={obj.key}
                                name="ritem"
                                value={obj.key}
                                onChange={(val) => {
																	setWillingnessQuestion1(val.target.value)
                                    console.log(val.target.value)
                                }}
                                className="textNormal"
                                checked={willingnessQuestion1 === obj.key}
                            />
                            <label className="textNormal radio-label" htmlFor={obj.key}>{obj.name}</label>
                        </div>
                    </div>
                ))}
				<div className="footer">
					<Footer
						navigation={props.history}
						next="/find-mintbox/risk-taking-willingness/2"
						params={props.location.state}
						saveData={{
							riskCapability: {
								willingnessQuestion1,
							}
						}}
						nextdisabled={nextdisabled} 
						buttonText="Next &#8594;"/>
				</div>
			</div>
		</div>
	}
}
