  ResetPasswordScreen, RewardsScreen, SetupPasswordScreen, SignScreen, SignupScreen, TransactionsScreen, WatchlistScreen, 
  GettingStarted, CapabilityPage1, CapabilityResult, CapabilityPage2, CapabilityPage3, CapabilityPage4, CapabilityPage5, CapabilityPage6, 
  WillingnessPage1, WillingnessPage2, WillingnessResult, PINScreen
