import { Button } from 'react-bootstrap';
import React from 'react';
import db from '../../../../libs/db';
import { setUserData } from '../../../../redux/CompleteProfile/actions';

const BottomBackNextBtn = ({
  next,
  navigation,
  params,
  nextdisabled,
  saveData,
}) => {

  const onNextPress = () => {
    if (saveData && !nextdisabled) {  
      db.get(db.model.userData).then((u) => {
        // u = JSON.parse(u);
        if (u) {
          Object.assign(u, saveData);
        } else {
          u = saveData;
        }
        console.log(u)
        setUserData(u);
        // navigation.navigate(next, params);
       navigation.push({pathname: next, state : params})
      });
    } else {
    //  navigation.navigate(next, params);
        navigation.push({pathname: next, state : params})
    }
  };

  // console.log(nextdisabled, saveData)

  return (
    <div
      style={{
        display: 'flex',
        flexDirection: 'row',
        flex: 1,
        justifyContent: 'space-between',
        width: '100%'
      }}>
      
      {next.toString().includes("final") ? 
        <Button
          variant="primary"
          width="100%"
          className="btn-footer"
          style={{ opacity: nextdisabled ? 0.4 : 1 }}
          onClick={() => (nextdisabled ? {} : onNextPress())}
        >
          Submit
        </Button>
        : <Button
          variant="primary"
          width="100%"
          className="btn-footer"
          style={{ opacity: nextdisabled ? 0.4 : 1 }}
          onClick={() => (nextdisabled ? {} : onNextPress())}
        >
          Next &#8594;
        </Button>}
         
    </div>
  );
};

export default BottomBackNextBtn;
