import React from 'react';
import { Spinner } from 'react-bootstrap';
import Footer from '../Footer';
import Header from '../Header';
import db from '../../../../../../libs/db';

export default function WillingnessPage2(props) {
	const RadioInputOptions = [
		{ key: '0', name: 'Moving to safe instruments completely and securing the accumulated corpus' },
		{ key: '1', name: 'Not making any changes and continuing with the portfolio for 2 more years for extra corpus' },
		{ key: '2', name: 'Moving around 50% of the corpus to safe instruments and staying invested with the other 50% thereby neither completely losing out on potential returns nor completely risking the entire corpus accumulated' },
]
	const [loading, setloading] = React.useState(true);
	const [nextdisabled, setnextDisabled] = React.useState(true);

	const [willingnessQuestion2,setWillingnessQuestion2]=React.useState("");

	const checkValidation = () => {
		if(willingnessQuestion2){
			setnextDisabled(false);
		} else {
			setnextDisabled(true)
		}
	};

	React.useEffect(() => {
		checkValidation();
	})

	React.useEffect(() => {
		db.get(db.model.userData).then((u) => {
				console.log(u)
				if (u) {
						if (u.riskCapability) {
							setWillingnessQuestion2(u.riskCapability.willingnessQuestion2 || '')
						}
				}
				setloading(false);
		});
		checkValidation();
	}, []);

	if (loading) {
		return (
			<div className="container">
				<Spinner
					as="span"
					animation="grow"
					size="sm"
					role="status"
					aria-hidden="true"
				/>
			</div>
		);
	} else {
		return <div className="container">
			<Header headerText="Risk-taking willingness" helperText="2/2 questions" navigation={props.history} />
			<div className="mt-4 mb-2">
				<div className="textHead" style={{marginBottom:'4%'}}>
					Your target retirement goal corpus has been accumulated 2 years in advance. 
					<br/><br/>
					What would your plan of action be?
				</div>
				{RadioInputOptions.map((obj) => (
                    <div key={obj.key} style={{ display: 'flex', flexDirection: 'row' }}>
                        <div className="radio-item">
                            <input
                                type="radio"
                                id={obj.key}
                                name="ritem"
                                value={obj.key}
                                onChange={(val) => {
																	setWillingnessQuestion2(val.target.value)
                                    console.log(val.target.value)
                                }}
                                className="textNormal"
                                checked={willingnessQuestion2 === obj.key}
                            />
                            <label className="textNormal radio-label" htmlFor={obj.key}>{obj.name}</label>
                        </div>
                    </div>
                ))}
				<div className="footer">
					<Footer
						navigation={props.history}
						next="/find-mintbox/risk-taking-willingness/result"
						params={props.location.state}
						saveData={{
							riskCapability: {
								willingnessQuestion2,
							}
						}}
						nextdisabled={nextdisabled} 
						buttonText="Next &#8594;"/>
				</div>
			</div>
		</div>
	}
}
