import React from 'react';
import { Modal, Button, Spinner } from 'react-bootstrap';
import Footer from '../Footer';
import Header from '../Header';
import db from '../../../../../../libs/db';
import colorsFinal from "../../../../../../theme/colors";
import { BsQuestionCircle } from 'react-icons/bs';
import { RiskLevelAggressive } from 'assets/SVG';

export default function WillingnessPageResult(props) {

	const [loading, setloading] = React.useState(true);
	const [nextdisabled, setnextDisabled] = React.useState(true);
	// dialog
	const [dshow, setdshow] = React.useState(false);
	const [ddata, setddata] = React.useState([]);
	React.useEffect(() => {
		setloading(false)
		setnextDisabled(false)
	})

	if (loading) {
		return (
			<div className="container">
				<Spinner
					as="span"
					animation="grow"
					size="sm"
					role="status"
					aria-hidden="true"
				/>
			</div>
		);
	} else {
		return <div className="container">
			<Header headerText="Risk analysis" helperText="" navigation={props.history} />
			<Modal
					show={dshow}
					onHide={() => setdshow(!dshow)}
					backdrop="static"
					keyboard={false}
					size="sm"
					aria-labelledby="contained-modal-title-vcenter"
					centered
			>
					<Modal.Body>
							<p className="textHead">{ddata[0]}</p>
							<p className="textNormal">{ddata[1]}</p>
					</Modal.Body>
					<Button variant="light" style={{ color: colorsFinal.brandPrimary }} onClick={() => setdshow(!dshow)}>
							Okay
					</Button>
			</Modal>
			<div className="mt-4 mb-2">
				<div style={{background:"#f1f1f1", borderRadius:10, textAlign:"center", padding:10}}>
					<div style={{marginBottom:10}}>
						<span className="textHelper">Your risk level</span>
						<BsQuestionCircle size={16} 
							style={{color:"#999999",marginLeft:"8px", background:"white", borderRadius:10}}
							onClick={() => {
									setddata(["", ""]);
									setdshow(true);
							}}
						/>
					</div>
					<img src={RiskLevelAggressive} alt="" />
					<div className="textHead" style={{fontSize:18, fontWeight:500}}>Aggressive</div>
				</div>
				<div className="footer">
					<Footer
						navigation={props.history}
						next="/find-mintbox/risk-taking-willingness/result"
						params={props.location.state}
						nextdisabled={nextdisabled} 
						buttonText="Show aggressive mintboxes"/>
				</div>
			</div>
		</div>
	}
}
