import React, { useEffect } from 'react'
import * as d3 from 'd3';
import DataFrame from 'libs/DataFrame';

const GRAPH_MARGIN = 20;
const SVGHeight = 300;
const SVGWidth = window.innerWidth - window.innerWidth % 10;
const height = SVGHeight - 2 * GRAPH_MARGIN;
const width = SVGWidth - 2 * GRAPH_MARGIN;
const labelArea = 15;

export default function LineChart(props) {
  let {data=[], style, svgStyle} = props;

  data = [
    {x: 0, y: 0},
    {x: 1, y: 1},
    {x: 2, y: 2},
    {x: 3, y: 3},
    {x: 4, y: 4},
    {x: 5, y: 5},
  ];
  const df = new DataFrame({data});

  const xDomain = [df.minXs(), df.maxXs()];
  const yDomain = [df.minYs(), df.maxYs()];

  const xRange = [labelArea, width - labelArea];
  const yRange = [height - labelArea, labelArea];

  const x = d3.scaleLinear().domain(xDomain).range(xRange);
  const y = d3.scaleLinear().domain(yDomain).range(yRange);

  const line = d3.line().x(d => x(d.x)).y(d => y(d.y))(data);

  const makeLine = () => {
    const svg = d3.select("#line-chart")
                .append("svg")
                .attr("width", width + labelArea)
                .attr("height", height + labelArea)
                .append("g")
                .attr("transform", "translate(" + labelArea + "," + 0 + ")");

    const xScale = d3.scaleLinear().domain(xDomain).range(xRange);
    const yScale = d3.scaleLinear().domain(yDomain).range(yRange);

    const x_axis = d3.axisBottom().scale(xScale);
    const y_axis = d3.axisLeft().scale(yScale);;
    
    svg.append('g').attr("transform", `translate(0, ${height - labelArea})`).call(x_axis);
    svg.append('g').attr("transform", `translate(${labelArea}, 0)`).call(y_axis);


    svg.append("path")
      .attr("fill", "none")
      .attr("stroke", "steelblue")
      .attr("stroke-width", 1.5)
      .attr("d", line)
  }

  useEffect(() => {
    makeLine()
  }, [])

  return (
    <div id='line-chart'></div>
  )
}
