import 'bootstrap/dist/css/bootstrap.min.css';
import React from "react";
import { isMobileOnly } from 'react-device-detect';
import { BrowserRouter as Router, Switch } from 'react-router-dom';
import { ToastContainer } from "react-toastify";
import 'react-toastify/dist/ReactToastify.css';
import WebHomeLayout from 'views/desktopView/route';
import cssVar from 'views/mobileView/styles';
import "./App.css";
import minbox from "./assets/SVG/mintbox_logo.svg";
import HomeLayout from "./views/mobileView/route";

class App extends React.Component {

  constructor(props) {
    super(props);
    this.state = {
      load: true,
    };
  }

  changeThemeVal() {
    var theme = window.localStorage.getItem("theme")
    if (theme === null || theme === undefined) {
      window.localStorage.setItem("theme", 'light')
      document.documentElement.setAttribute("data-theme", "light")
      document.body.style = `background: ${cssVar.getPropertyValue("--bg")};`;
    } else {
      if (theme === 'dark') {
        document.documentElement.setAttribute("data-theme", "dark")
        document.body.style = `background: ${cssVar.getPropertyValue("--bg")};`;
      } else {
        document.documentElement.setAttribute("data-theme", "light")
        document.body.style = `background: ${cssVar.getPropertyValue("--bg")};`;
      }
    }
  }

  componentDidMount() {
    this.changeThemeVal()
    this.setState({
      load: false,
    });
  }

  render() {
    if (isMobileOnly) {
      return (
        <div className="body">
          <ToastContainer />
          <Router>
            {/* <BottomNavBar/> */}
            <Switch>
              <HomeLayout />
            </Switch>
          </Router>
        </div>
      );
    } else {
      return (
        <div className="body">
          <ToastContainer />
          <Router>
            {/* <BottomNavBar/> */}
            <Switch>
              <WebHomeLayout />
            </Switch>
          </Router>
        </div>
      );
    }
  }
}

export default App;
