import db from 'libs/db';
import React, { useEffect, useState } from 'react';
import { Button, Spinner } from 'react-bootstrap';
import { FiChevronDown, FiChevronUp, FiInfo } from 'react-icons/fi';
import AdminServices from 'services/api/AdminServices';
import TabBar from 'views/mobileView/components/atoms/TabBar';
import TextInput from 'views/mobileView/components/atoms/TextInput';
import MainHeader from 'views/mobileView/components/molecules/Headers/MainHeader';
import cssVar from 'views/mobileView/styles';

export default function AccountDetailsScreen(props) {
  const [user, setUser] = useState({});
  const [userData, setUserData] = useState([{}]);
  const [loading, setloading] = useState(true);

  const getData = async (user) => {
    let da = await AdminServices.getClient(user.id).then((res) => JSON.stringify(res));
    da = JSON.parse(da);
    const aa = JSON.parse(da.data);
    await setUserData(aa);
    setloading(false);
  };

  useEffect(() => {
    db.get(db.model.user, true).then((p) => {
      setUser(p.userData);
      if (p.userData.profileComplete) {
        getData(p.userData);
      } else {
        setloading(false);
      }
    });
  }, []);

  const CustomTextInput = (props) => {
    const {
      value,
      label,
      editable,
      keyboardType = 'email-address',
      icon = 'pencil-outline'
    } = props;
    const [changed, setChanged] = useState(false);
    const [valued, setValued] = useState(value);
    const [defvalued, setdefValued] = useState(value);

    return (
      <TextInput
        label={label}
        editable={editable}
        keyboardType={keyboardType}
        value={valued}
        onBlur={
          () => (valued !== defvalued ? changed ? setdefValued(valued) : setValued(defvalued) : console.log('no chnage'))
        }
        onChangeText={(t) => setValued(t)}
      // right={
      //   icon ? (
      //     <TextInput.Icon
      //       name={icon}
      //       style={{ paddingTop: 10, zIndex: 2 }}
      //       color={cssVar.getPropertyValue('--primary')}
      //       size={20}
      //       onClick={valued === defvalued ? () => {} : () => {
      //         setChanged(true);
      //       }} />
      //   ) : (
      //     false
      //   )
      // } 
      />
    );
  };

  const ContactDetails = () => (
    <div className='container' style={{ display: 'flex' }}>
      <CustomTextInput
        editable
        label="Display name"
        value={userData[0].Client[0].DisplayName} />
      <CustomTextInput label="Email ID" value={userData[0].Client[0].Email} icon={false} />
      <CustomTextInput label="Mobile number" value={userData[0].Client[0].MobileNumber} icon={false} />
    </div>
  );

  const ContactDetails2 = () => (
    <div className='container' style={{ display: 'flex' }}>
      <CustomTextInput
        editable label="Display name" value={user.name} />
      <CustomTextInput
        editable label="Email ID" value={user.email} />
    </div>
  );

  const KYCDetails = () => (
    <div className='container d-flex py-2'>
      {userData[0].Client[0].TaxStatus === "On Behalf Of Minor" ? (
        <div className='mx-2' style={{ flex: 1 }}>
          <span className='textNormal'>Legal guardian details</span>
          <CustomTextInput
            editable label="Guardian KYC name" value={userData[0].Client[0].GuardianName}
            icon={false} />
          <CustomTextInput
            editable
            label="Guardian PAN"
            value={userData[0].Client[0].GuardianPAN}
            icon={false} />
          <CustomTextInput
            editable label="KYC status" value="Registered"
            icon={false} />
        </div>
      ) : (
        <div className='mx-1 flex-fill'>
          <span className='textNormal'>Primary Holder</span>
          <CustomTextInput
            editable label="KYC name" value={userData[0].Client[0].DisplayName}
            icon={false} />
          <CustomTextInput
            editable
            label="PAN"
            value={userData[0].Client[0].PAN}
            icon={false} />
          <CustomTextInput
            editable label="KYC status" value="Registered"
            icon={false} />
        </div>
      )}

    </div>
  );

  const BankAccountDetails = () => {
    const [visible, setvisible] = useState(false);
    const [banks, setbanks] = useState([]);

    const hideDialog = () => setvisible(false);
    const showDialog = () => setvisible(true);

    const [primaryBanks, setprimaryBanks] = useState([]);
    const [secondaryBanks, setsecondaryBanks] = useState([]);

    const [loadingBanks, setloadingBanks] = useState(true);

    useEffect(() => {
      setbanks(userData[4].BankInfo);
      setprimaryBanks(userData[4].BankInfo.filter((b) => b.IsDefault === 1));
      setsecondaryBanks(userData[4].BankInfo.filter((b) => b.IsDefault !== 1));
      setloadingBanks(false);
    }, []);

    const DeleteBankAccount = async (bank) => {
      setloading(true);
      const res = await AdminServices.updateBankAccount({
        uid: bank.UID,
        clientId: bank.ClientId,
        isDeleted: 1
      });
      if (res.type) {
        getData(user);
      }
      setloading(false);
    };

    const MakeAccountPrimary = async (bank) => {
      setloading(true);
      if (primaryBanks.length < 0) {
        const res = await AdminServices.updateBankAccount({
          ...bank,
          isDefault: 1
        });
        if (res.type) getData(user);
      }
      setloading(false);
    };

    if (loadingBanks) {
      return (
        <div style={{ flex: 1, justifyContent: 'center', alignContent: 'center' }}>
          <Spinner
            color={cssVar.getPropertyValue('--primary')}
            size="small"
            style={{ alignSelf: 'center' }} />
        </div>
      );
    }

    const MakePrimaryModal = () => (
      <div></div>
      // <Portal>
      //   <Dialog visible={visible} onDismiss={hideDialog} style={{ borderRadius: 10 }}>
      //     <span style={{ ...s.m, padding: 16 }}>
      //       The primary bank account is linked to all fresh investments you make.
      //     </span>
      //     <div
      //       style={{ width: '100%', padding: 16, ...s.mt('1%'), borderTopWidth: 1, borderColor: '#E5E5E5' }}
      //       onClick={hideDialog}>
      //       <div style={{ ...s.rowVC, justifyContent: 'center' }}>
      //         <span style={{ ...s.btnLabelWithoutPad, textAlign: 'center', color: cssVar.getPropertyValue('--primary') }}>
      //           Okay
      //         </span>
      //       </div>
      //     </div>
      //   </Dialog>
      // </Portal>
    );

    const BankCard = ({ bank, active = false }) => {
      const [isOpen, setIsOpen] = React.useState(active);
      return (
        <div className='my-1' style={{ border: '1px solid #E5E5E5', padding: 12, borderRadius: 8 }}>
          <div className='d-flex justify-content-between'>
            <div className='d-flex' style={{ width: '70%' }}>
              <FiInfo color={cssVar.getPropertyValue('--primary')} size={20} />
              <span className='textNormal px-3' style={{ flexWrap: 'wrap-reverse', width: '90%' }} >
                {bank.BankName}
              </span>
            </div>
            <div onClick={() => setIsOpen((s) => !s)} className='mx-2'>
              <div className='d-flex align-items-center'>
                <span className='textHelper w-100' style={{ color: cssVar.getPropertyValue('--primary') }}>
                  Show more
                </span>
                {isOpen ? <FiChevronUp color={cssVar.getPropertyValue('--primary')} size={20} /> : <FiChevronDown color={cssVar.getPropertyValue('--primary')} size={20} />}
              </div>
            </div>
          </div>
          <span className='textHelper' style={{ color: cssVar.getPropertyValue('--placeholder') }}>
            Account number: <span className='textHelper'>{bank.AccountNumber}</span>
          </span>
          {isOpen && (
            <>
              <div>
                <div className='textHelper pt-2' style={{ color: cssVar.getPropertyValue('--placeholder') }}>
                  IFSC: <span className='textHelper'>{bank.IFSC}</span>
                </div>
                <div className='textHelper pt-2' style={{ color: cssVar.getPropertyValue('--placeholder') }}>
                  Bank mandate type:{' '}
                  <span className='textHelper'>{bank.mandate_type}</span>
                </div>
                <div className='textHelper pt-2' style={{ color: cssVar.getPropertyValue('--placeholder') }}>
                  Bank mandate status:{' '}
                  <span className='textHelper'>{bank.mandate_status}</span>
                </div>
                <div className='textHelper pt-2' style={{ color: cssVar.getPropertyValue('--placeholder') }}>
                  Bank mandate limit:{' '}
                  <span className='textHelper'>{bank.mandate_limit}</span>
                </div>
                <div className='textHelper pt-2' style={{ color: cssVar.getPropertyValue('--placeholder') }}>
                  Default in folios:{' '}
                  <span className='textHelper'>{bank.default_folio}</span>
                </div>
              </div>
              <div className='d-flex mt-2'>
                <Button
                  variant='primary'
                  className='btn-normal'
                  style={{ flex: 1, width: '100%' }}
                  labelStyle={{ color: cssVar.getPropertyValue('--error') }}
                  onClick={() => DeleteBankAccount(bank)}>
                  Delete
                </Button>
                <div style={{ padding: 10 }} />
                <Button
                  style={{ flex: 1, width: '100%' }}
                  variant='primary'
                  className='btn-normal'
                  labelStyle={{ color: cssVar.getPropertyValue('--primary') }}
                  onClick={() => MakeAccountPrimary(bank)}>
                  Make Primary
                </Button>
              </div>
            </>
          )}
        </div>
      );
    };

    return (
      <div className='container'>
        <div className='d-flex mt-2 align-items-center'>
          <span className='textHead' style={{ marginRight: 5 }}>Primary account</span>
          <FiInfo
            color={cssVar.getPropertyValue('--primary')}
            size={20}
            onClick={showDialog} />
        </div>
        <div className='my-2'>
          {primaryBanks.map((bank, i) => <BankCard key={`sda${i}`} bank={bank} />)}
        </div>
        <div className='row align-items-center'>
          <span className='textHead pr-2'>Other account(s)</span>
        </div>
        <div className='my-2'>
          {secondaryBanks.map((bank, i) => <BankCard bank={bank} />)}
        </div>
        <div style={{ flex: 1, justifyContent: 'flex-end' }}>
          {banks.length < 3 ? (
            <>
              <span className='textHelper' style={{ color: cssVar.getPropertyValue('--secondary'), textAlign: 'center' }}>
                You can add up to 3 bank accounts
              </span>
              <Button
                variant='primary'
                className='btn-normal'
                style={{ borderColor: cssVar.getPropertyValue('--primary'), width: '90%', margin: 15 }}
                onClick={() => props.history.push('add-bank-account')}>
                Add a bank account
              </Button>
            </>
          ) : (
            <div className='textHelper text-center w-100 my-2' style={{ color: cssVar.getPropertyValue('--secondary'), textAlign: 'center' }}>
              Delete an account to add an account
            </div>
          )}
        </div>
        <MakePrimaryModal />
      </div>
    );
  };

  const OtherDetails = () => (userData[3].Nominees[0] === undefined
    ? (
      <div className='container'>
        <CustomTextInput
          editable={false} label="Account holding mode" value="Single" />

      </div>
    )
    : (
      <div className='container'>
        <CustomTextInput
          editable={false} label="Account holding mode" value="Single" />
        <CustomTextInput
          editable label="Nominee name" value={userData[3].Nominees[0].Name} />
        <CustomTextInput
          editable label="Nominee relationship" value={userData[3].Nominees[0].Relation} />
      </div>
    ));

  if (loading) {
    return (
      <div style={{ flex: 1, justifyContent: 'center', alignContent: 'center' }}>
        <Spinner
          color={cssVar.getPropertyValue('--primary')}
          size="small"
          style={{ alignSelf: 'center' }} />
      </div>
    );
  }

  const keyTitles = [
    { key: 'Contact', title: 'Contact' },
    { key: 'KYC', title: 'KYC' },
    { key: 'Bank', title: 'Bank' },
    { key: 'Other', title: 'Other' }
  ];

  const keyComponents = {
    Contact: ContactDetails,
    KYC: KYCDetails,
    Bank: BankAccountDetails,
    Other: OtherDetails
  };

  return (
    <div style={{ flex: 1 }}>
      <MainHeader header="Account details" navigation={props.history} />
      {user.profileComplete ? (
        <TabBar keyTitles={keyTitles} keyComponents={keyComponents} />
      ) : (
        <div>
          {/* <Accordion title="Contact details" left={<Portfoilo />} right={(e) => e ? <FiChevronUp size={20} color={cssVar.getPropertyValue('--primary')} /> : <FiChevronDown size={20} color={cssVar.getPropertyValue('--primary')} /> }> */}
          <ContactDetails2 />
          {/* </Accordion> */}
          <div style={{ flex: 1, justifyContent: 'flex-end' }}>
            <Button
              className='btn-normal'
              variant='primary'
              style={{ borderColor: cssVar.getPropertyValue('--primary'), width: '90%', margin: 15 }}>
              Contact Support
            </Button>
          </div>
        </div>
      )}
    </div>
  );
}
