import React from 'react';
import { Spinner } from 'react-bootstrap';
import Footer from '../Footer';
import Header from '../Header';
import db from '../../../../../../libs/db';

export default function CapabilityPageResult(props) {

	const [loading, setloading] = React.useState(true);
	const [nextdisabled, setnextDisabled] = React.useState(true);

	React.useEffect(() => {
		setloading(false)
		setnextDisabled(false)
	})

	if (loading) {
		return (
			<div className="container">
				<Spinner
					as="span"
					animation="grow"
					size="sm"
					role="status"
					aria-hidden="true"
				/>
			</div>
		);
	} else {
		return <div className="container">
			<Header headerText="Risk-taking capability" helperText="" navigation={props.history} />
			<div className="mt-4 mb-2">
				<div className="textHead" style={{marginBottom:20}}>The critical aspects of your personal finance are sound</div>
				<div className="textHead" style={{marginBottom:20}}>We think that you are capable of taking <span style={{color:"red",fontSize:'16px'}}>aggressive</span> investment risks</div>
				<div className="textHead" style={{marginBottom:20}}>Please continue and measure your risk taking willingness now for full risk analysis</div>
				<div className="footer">
					<Footer
						navigation={props.history}
						next="/find-mintbox/risk-taking-willingness/1"
						params={props.location.state}
						nextdisabled={nextdisabled} 
						buttonText="Continue"/>
				</div>
			</div>
		</div>
	}
}
