import React from 'react';
import { Spinner } from 'react-bootstrap';
import CustomDropDown from 'views/mobileView/components/molecules/DropDown/CustomDropDown';
import db from '../../../../../../libs/db';
import AdminServices from '../../../../../../services/api/AdminServices';
import BottomBackNextBtn from '../../../../components/atoms/BottomBackNextBtn';
import { dropdownCustom } from '../../../../components/atoms/DataCustomizer';
import ProfileCompleteHeader from '../../../../components/molecules/Headers/ProfileCompletionHeader';


export default function Fatca(props) {

    const [loading, setloading] = React.useState(true);
    const [income, setincome] = React.useState([]);
    const [wealth, setwealth] = React.useState([]);
    const [occupation, setoccupation] = React.useState([]);
    const [nextdisabled, setnextDisabled] = React.useState(true);

    const [incomeList, setincomeList] = React.useState([]);
    const [wealthList, setwealthList] = React.useState([]);
    const [occupationList, setoccupationList] = React.useState([]);

    const dataAdult = {
        head: 'FATCA'
    };

    const dataMinor = {
        head: 'Legal guardian FATCA'
    };

    const dataNri = {
        head: 'FATCA'
    };

    const checkValidation = () => {
        if (
            Object.entries(income).length != 0 &&
            Object.entries(wealth).length != 0 &&
            Object.entries(occupation).length != 0
        ) {
            setnextDisabled(false);
        } else {
            setnextDisabled(true);
        }
    };

    const getData = async () => {
        try {
            await AdminServices.getOccupation()
                .then(async (res) => {
                    // console.log('data1: ', res);
                    const data = await dropdownCustom(res.data, ['UID', 'Occupation']);
                    setoccupationList(data);
                })
                .then(async () => {
                    await AdminServices.getAnnualIncome()
                        .then(async (res) => {
                            // console.log('data2: ', res);

                            const data = await dropdownCustom(res.data, [
                                'UID',
                                'IncomeSlab'
                            ]);
                            setincomeList(data);
                        })
                        .then(async () => {
                            await AdminServices.getSourceofWealth()
                                .then(async (res) => {
                                    // console.log('data3: ', res);

                                    const data = await dropdownCustom(res.data, [
                                        'UID',
                                        'Source'
                                    ]);
                                    setwealthList(data);
                                })
                                .then(() => {
                                    setloading(false);
                                });
                        });
                });
        } catch (error) {
            console.log(error);
        }
    };

    React.useEffect(() => {
        checkValidation();
    });

    React.useEffect(() => {
        db.get(db.model.userData).then((u) => {
            if (u) {
                // u = JSON.parse(u);
                console.log(u)
                if (u.fatca) {
                    setincome(u.fatca.income || '');
                    setwealth(u.fatca.wealth || '');
                    setoccupation(u.fatca.occupation || '');
                    setoccupationList(u.fatca.occupationList || []);
                    setincomeList(u.fatca.incomeList || []);
                    setwealthList(u.fatca.wealthList || []);

                    setloading(false);
                } else {
                    getData().then(() => {
                        setloading(false);
                    });
                }
            }
        });
        checkValidation();
    }, []);

    if (loading) {
        return (
            <div className="center-screen">
                <Spinner
                    as="span"
                    animation="grow"
                    size="sm"
                    role="status"
                    aria-hidden="true"
                />
            </div>
        );
    } else {
        return <div className="container">
            <ProfileCompleteHeader header="FATCA" navigation={props.history} percentage={40} />
            <div className="mt-4 mb-2">
                <CustomDropDown
                    label="Annual income"
                    initial={income}
                    items={incomeList}
                    onChange={(item) => setincome(item)} />
                <CustomDropDown
                    label="Source of wealth"
                    initial={wealth}
                    items={wealthList}
                    onChange={(item) => setwealth(item)} />
                <CustomDropDown
                    label="Occupation"
                    initial={occupation}
                    items={occupationList}
                    onChange={(item) => setoccupation(item)} />
                {/* <div className="dropdown">


                    <input
                        className="dropdown-select"
                        list="income"
                        placeholder="Annual income"
                        onChange={(item) => {
                            setincome(item)
                            console.log(income)
                        }}
                    />
                    <datalist id="income">
                        {incomeList.map((data) => {
                            return <option itemID={data.id} >{data.name}</option>
                        })}
                    </datalist>

                    <input
                        className="dropdown-select"
                        list="wealth"
                        placeholder="Source of wealth"
                        onChange={(item) => {
                            setwealth(item)
                            console.log(item)
                        }}
                    />
                    <datalist id="wealth">
                        {wealthList.map((data) => {
                            return <option itemID={data.id} >{data.name}</option>
                        })}
                    </datalist>

                    <input
                        className="dropdown-select"
                        list="occupation"
                        placeholder="Occupation"
                        onChange={(item) => {
                            setoccupation(item)
                            console.log(item)
                        }}
                    />
                    <datalist id="occupation">
                        {occupationList.map((data) => {
                            return <option itemID={data.id} >{data.name}</option>
                        })}
                    </datalist>

                </div>
             */}
            </div>
            <div className="footer">
                {props.location.state.selection === 'adult' ? (
                    <BottomBackNextBtn
                        navigation={props.history}
                        next="/complete_pro/nominee"
                        params={props.location.state}
                        saveData={{
                            fatca: {
                                income: income,
                                wealth: wealth,
                                occupation: occupation,
                                incomeList: incomeList,
                                wealthList: wealthList,
                                occupationList: occupationList
                            }
                        }}
                        nextdisabled={nextdisabled}
                    />
                ) : props.location.state.selection === 'minor' ? (
                    <BottomBackNextBtn
                        navigation={props.history}
                        next="/complete_pro/bank"
                        params={props.location.state}
                        saveData={{
                            fatca: {
                                income: income,
                                wealth: wealth,
                                occupation: occupation,
                                incomeList: incomeList,
                                wealthList: wealthList,
                                occupationList: occupationList
                            }
                        }}
                        nextdisabled={nextdisabled}
                    />
                ) : (
                    <BottomBackNextBtn
                        navigation={props.history}
                        next="/complete_pro/nominee"
                        params={props.location.state}
                        saveData={{
                            fatca: {
                                income: income,
                                wealth: wealth,
                                occupation: occupation,
                                incomeList: incomeList,
                                wealthList: wealthList,
                                occupationList: occupationList
                            }
                        }}
                        nextdisabled={nextdisabled}
                    />
                )}
            </div>

        </div>
    }
}

