import DataFrame from 'libs/DataFrame';
import React, { useEffect, useRef } from 'react';
import * as d3 from 'd3';

const GRAPH_MARGIN = 20;
const SVGHeight = 300;
const SVGWidth = window.innerWidth - window.innerWidth % 10;
const height = SVGHeight - 2 * GRAPH_MARGIN;
const width = SVGWidth - 2 * GRAPH_MARGIN;
const labelArea = 15;

export default function StackedBarChart(props) {
  const {data=Data} = props;
  const chart = useRef();

  const df = new DataFrame({data});
  const cols = Object.keys(data[0]).slice(1);
  const series = d3.stack().keys(cols)(data);

  series.forEach((s, i) => {
    s.forEach((l, j) => {
      l[2] = data[j].category;
      l[3] = cols[i];
    });
  });

  const xDomain = df.cols.category
  const yDomain = [0, 100];

  const xRange = [labelArea, width - labelArea];
  const yRange = [height - labelArea, labelArea];

  const x = d3.scaleBand().domain(xDomain).range(xRange).padding(0.2);
  const y = d3.scaleLinear().domain(yDomain).range(yRange);
  const color = d3.scaleOrdinal().domain(cols).range(d3.schemeTableau10);


  const make = () => {
    const svg = d3.select(chart.current)
                  .attr("width", width + labelArea)
                  .attr("height", height + labelArea)
                  .append("g")
                  .attr("transform", `translate(${labelArea}, 0)`);
    
    const x_axis = d3.axisBottom(x).tickSizeOuter(0);
    const y_axis = d3.axisLeft(y);

    svg.append("g").attr("transform", `translate(0, ${height - labelArea})`).call(x_axis)
    svg.append('g').attr("transform", `translate(${labelArea}, 0)`).call(y_axis).attr('class', 'y-axis');

    svg.append("g")
        .selectAll("g")
        .data(series)
        .join("g")
          .attr("fill", d => color(d[3]))
        .selectAll("rect")
        .data(d => d)
        .join("rect")
          .attr("x", d => x(d[2]))
          .attr("y", d => y(d[1]))
          .attr("height", d => y(d[0]) - y(d[1]))
          .attr("width", x.bandwidth())
  }

  useEffect(() => {
    make()
  }, [])

  return (
    <div id='bar-chart'>
      <svg ref={chart} />
    </div>
  )
}

const Data = [
  {category: 'Jan', yes: 10, no: 1, maybe: 12},
  {category: 'Feb', yes: 21, no: 5, maybe: 2},
  {category: 'Mar', yes: 23, no: 1, maybe: 13},
  {category: 'Apr', yes: 13, no: 3, maybe: 6},
  {category: 'May', yes: 14, no: 13, maybe: 8},
  {category: 'June', yes: 24, no: 1, maybe: 22},
  {category: 'July', yes: 26, no: 6, maybe: 1},
  {category: 'Sep', yes: 45, no: 7, maybe: 2},
  {category: 'Oct', yes: 4, no: 1, maybe: 15},
];
