import React from 'react';
import { Spinner } from 'react-bootstrap';
import FloatingTextInput from 'views/mobileView/components/atoms/FloatingTextInput';
import db from '../../../../../../libs/db';
import AdminServices from '../../../../../../services/api/AdminServices';
import BottomBackNextBtn from '../../../../components/atoms/BottomBackNextBtn';
import { dropdownCustom } from '../../../../components/atoms/DataCustomizer';
import ProfileCompleteHeader from '../../../../components/molecules/Headers/ProfileCompletionHeader';
import cssVar from '../../../../styles';
import $ from 'jquery';
import NumericInput from 'views/mobileView/components/atoms/NumericInput';


const Allocation = (props) => {

    const [jointyes, setjointYes] = React.useState('no');
    const [addnomine, setaddnomine] = React.useState('no');

    const [joint, setJoint] = React.useState('joint');
    const [name, setname] = React.useState('');
    const [income, setincome] = React.useState('');
    const [wealth, setwealth] = React.useState('');
    const [occupation, setoccupation] = React.useState('');

    const [noName, setnoName] = React.useState('');
    const [relation, setrelation] = React.useState('');
    const [dob, setdob] = React.useState('');

    const [nextdisabled, setnextDisabled] = React.useState(false);
    const [loading, setloading] = React.useState(true);
    const [nomloading, setnmloading] = React.useState(false);

    const [incomeList, setincomeList] = React.useState([]);
    const [wealthList, setwealthList] = React.useState([]);
    const [nomAlloList, setnomAlloList] = React.useState([]);



    const getData = async () => {
        try {
            await AdminServices.getOccupation()
                .then(async (res) => {
                    // console.log('data1: ', res);
                    const data = await dropdownCustom(res.data, ['UID', 'Occupation']);
                    // setoccupationList(data);
                })
                .then(async () => {
                    await AdminServices.getAnnualIncome()
                        .then(async (res) => {
                            // console.log('data2: ', res);

                            const data = await dropdownCustom(res.data, [
                                'UID',
                                'IncomeSlab'
                            ]);
                            setincomeList(data);
                        })
                        .then(async () => {
                            await AdminServices.getSourceofWealth()
                                .then(async (res) => {
                                    // console.log('data3: ', res);

                                    const data = await dropdownCustom(res.data, [
                                        'UID',
                                        'Source'
                                    ]);
                                    setwealthList(data);
                                })
                                .then(() => {
                                    setloading(false);
                                });
                        });
                });
        } catch (error) {
            console.log(error);
        }
    };

    const isValidAllo = () => {
        var sum = 0;
        nomAlloList.map((data, index) => {
            sum += parseInt(data.nomAllocate);
        })
        if (sum !== 100) {
            setnextDisabled(true)
        } else {
            setnextDisabled(false)
        }
    }

    React.useEffect(() => {
        setnmloading(false)
    }, [nomloading])

    const changeData = (index, text) => {
        nomAlloList[index].nomAllocate = text
        setnomAlloList(nomAlloList)
        isValidAllo()
        setnmloading(true)
    }


    const delNominee = (index) => {
        var a = nomAlloList;
        a.splice(index, 1)
        setnomAlloList(a)
        setnmloading(true)
    }

    React.useEffect(() => {

        db.get(db.model.userData).then((u) => {
            if (u) {
                // u = JSON.parse(u);

                const r1 = u.joint;
                // setoccupationList(u.fatca.occupationList || []);
                // setincomeList(u.fatca.incomeList || []);
                // setwealthList(u.fatca.wealthList || []);
                if (r1) {
                    const r2 = u.joint.jointData;
                    // if (r2.jointvalue === 'yes') {
                    //   setjointYes(r2.jointvalue || ''), setname(r2.jointUser.name || '');
                    //   setdob(r2.jointUser.dob || '');
                    //   setincome(r2.jointUser.fatca.income || '');
                    //   setwealth(r2.jointUser.fatca.wealth || '');
                    //   setoccupation(r2.jointUser.fatca.occupation || '');
                    //   setJoint(r2.jointUser.accountType || '');
                    // } else {
                    //   setjointYes(r2.jointvalue || '');
                    // }

                    console.log(u)
                    if (r2.nominevalue === 'yes') {
                        setaddnomine(r2.nominevalue || '')
                        setnomAlloList(r2.nomineData)
                    } else {
                        setaddnomine(r2.nominevalue || '');
                    }
                }
                // isValidAllo()
                setloading(false);

            }
        });
    }, []);




    if (loading) {
        return (
            <div className="center-screen">
                <Spinner
                    as="span"
                    animation="grow"
                    size="sm"
                    role="status"
                    aria-hidden="true"
                />
            </div>
        );
    } else {
        return (
            <div className="container">
                <ProfileCompleteHeader header="Nominee allocation" navigation={props.history} percentage={60} />
                <div className="mt-4 mb-2">
                    <span className="textHead">Please specify nominee allocation</span>
                    <div
                        style={{
                            display: 'flex',
                            flexDirection: 'column'
                        }}>
                        {
                            nomAlloList.map((data, index,) => {
                                return (
                                    <div className="my-2" style={{
                                        display: 'flex',
                                        flexDirection: 'row',
                                    }}>
                                        <div style={{
                                            display: 'flex',
                                            flexDirection: 'column',
                                            flex: 1,
                                            justifyContent: "center"
                                        }} >
                                            <span style={{ marginBottom: 0 }}>{data.noName}</span>
                                            <span style={{ color: cssVar.getPropertyValue('--red') }} onClick={() => { delNominee(nomAlloList.length - 1); }}>Remove</span>
                                        </div>
                                        <div style={{ display: 'flex', flex: 1, justifyContent: "center", alignItems: "center" }}>
                                            <NumericInput
                                                onLimitReached={(isMax, msg) => console.log(isMax, msg)}
                                                id={"allo" + index}
                                                minValue={1}
                                                maxValue={100}
                                                value={data.nomAllocate}
                                                onChange={value => changeData(index, value)}
                                            />
                                        </div>
                                    </div>
                                )
                            })
                        }
                        {nextdisabled ? <span style={{
                            marginTop: "30px",
                            padding: "11px",
                            backgroundColor: cssVar.getPropertyValue('--red') + '33',
                            textAlign: 'center',
                            color: cssVar.getPropertyValue('--red')
                        }}>Sum should be 100</span> : null}
                    </div>
                </div>


                <div className="footer">
                    <BottomBackNextBtn
                        next="/complete_pro/bank"
                        navigation={props.history}
                        params={props.location.state}
                        saveData={{
                            joint: {
                                jointData: {
                                    jointvalue: jointyes,
                                    jointUser: {
                                        name,
                                        dob,
                                        fatca: {
                                            income,
                                            wealth,
                                            occupation
                                        },
                                        accountType: joint
                                    },
                                    nominevalue: addnomine,
                                    nomineData: nomAlloList
                                }
                            }
                        }}
                        nextdisabled={nextdisabled} />
                </div>
            </div>
        );
    }
};

export default Allocation;
