import { ManWithLadder } from 'assets/SVG';
import React from 'react';
import { Button, Form, Spinner } from 'react-bootstrap';
import FloatingTextInput from 'views/mobileView/components/atoms/FloatingTextInput';
import InfoCard from 'views/mobileView/components/molecules/Cards/InfoCard';
import MainHeader from 'views/mobileView/components/molecules/Headers/MainHeader';


export default function ModeOfInvestment(props) {


    const [isSip, setIsSip] = React.useState(false);
    const [sip, setsip] = React.useState(100000);
    const [islumpsum, setIslumpsum] = React.useState(false);
    const [lumpsum, setlumpsum] = React.useState(100000);
    const [loading, setloading] = React.useState(true);

    React.useEffect(() => {
        setloading(false)
    }, [])

    if (loading) {
        return (
            <div className="center-screen">
                <Spinner
                    as="span"
                    animation="grow"
                    size="sm"
                    role="status"
                    aria-hidden="true"
                />
            </div>
        );
    } else {
        return (
            <div style={{ flex: 1 }}>
                <MainHeader
                    header="Long term wealth creation"
                    navigation={props.history}
                />
                <div>
                    <InfoCard
                        svg={ManWithLadder}
                        title="You are almost there!"
                        text="Please answer the following questions. We will find the best possible options for you." gradient={{
                            colors: ['#F2FBFB88', '#F2FBFB']
                        }} />
                    <div className="container">
                        <h3 className="textHead">Mode of investment</h3>
                        <div className="checkbox-item">
                            <input
                                type="checkbox"
                                id="citem1"
                                name="citem"
                                value={isSip}
                                status={isSip ? 'checked' : 'unchecked'}
                                onClick={() => setIsSip((z) => !z)}
                                className="textNormal"
                            />
                            <label className="textNormal check-label" htmlFor="citem1">SIP</label>
                        </div>
                        {isSip && (
                            <div>
                                <FloatingTextInput
                                    type="text"
                                    label="SIP amount"
                                    id="sip-amount"
                                    keyboardType="number-pad"
                                    autoCapitalize="words"
                                    value={sip}
                                    onChange={(v) => setsip(v.target.value)}
                                />
                            </div>
                        )}
                        <div className="checkbox-item">
                            <input
                                type="checkbox"
                                id="citem2"
                                name="citem"
                                value={islumpsum}
                                status={islumpsum ? 'checked' : 'unchecked'}
                                onClick={() => setIslumpsum((z) => !z)}
                                className="textNormal"
                            />
                            <label className="textNormal check-label" htmlFor="citem2">Lumpsum</label>
                        </div>
                        {islumpsum && (
                            <div>
                                <FloatingTextInput
                                    type="text"
                                    label="SIP amount"
                                    id="sip-amount"
                                    keyboardType="number-pad"
                                    autoCapitalize="words"
                                    value={sip}
                                    onChange={(v) => setsip(v.target.value)}
                                />
                            </div>
                        )}
                    </div>
                </div>

                <div className="footer" style={{bottom: "50px"}}>
                    <Button
                        variant="primary"
                        className="btn-footer"
                        disabled={isSip == false || islumpsum == false }
                        onClick={() => props.history.push('/invest/mode_of_investment')}
                    >
                        Next
                    </Button>
                </div>
            </div>
        );
    }
}
