import React from 'react';
import { Modal, Button, Spinner } from 'react-bootstrap';
import FloatingTextInput from 'views/mobileView/components/atoms/FloatingTextInput';
import Footer from '../Footer';
import Header from '../Header';
import { Chip } from '@mui/material';
import db from '../../../../../../libs/db';
import colorsFinal from "../../../../../../theme/colors";
import { BsQuestionCircle } from 'react-icons/bs';

export default function CapabilityPage5(props) {

	const chipset = [
		{ "key": "0", "name": "No life insurance" },
		{ "key": "5000000", "name": "50 Lakh" },
		{ "key": "10000000", "name": "1 Crore" }
	]
	const [loading, setloading] = React.useState(true);
	const [nextdisabled, setnextDisabled] = React.useState(true);

	const [lifeInsurance,setLifeInsurance]=React.useState("");
	// dialog
	const [dshow, setdshow] = React.useState(false);
	const [ddata, setddata] = React.useState([]);
	const checkValidation = () => {
		if(lifeInsurance){
			setnextDisabled(false);
		} else {
			setnextDisabled(true)
		}
	};

	React.useEffect(() => {
		checkValidation();
	})

	React.useEffect(() => {
		db.get(db.model.userData).then((u) => {
				console.log(u)
				if (u) {
						if (u.riskCapability) {
							setLifeInsurance(u.riskCapability.lifeInsurance || '')
						}
				}
				setloading(false);
		});
		checkValidation();
	}, []);

	if (loading) {
		return (
			<div className="container">
				<Spinner
					as="span"
					animation="grow"
					size="sm"
					role="status"
					aria-hidden="true"
				/>
			</div>
		);
	} else {
		return <div className="container">
			<Header headerText="Risk-taking capability" helperText="5/6 questions" navigation={props.history} />
			<Modal
					show={dshow}
					onHide={() => setdshow(!dshow)}
					backdrop="static"
					keyboard={false}
					size="sm"
					aria-labelledby="contained-modal-title-vcenter"
					centered
			>
					<Modal.Body>
							<p className="textHead">{ddata[0]}</p>
							<p className="textNormal">{ddata[1]}</p>
					</Modal.Body>
					<Button variant="light" style={{ color: colorsFinal.brandPrimary }} onClick={() => setdshow(!dshow)}>
							Okay
					</Button>
			</Modal>
			<div className="mt-4 mb-2">
				<div className="textHead">What is the cover of your life insurance?
					<BsQuestionCircle size={16} 
						style={{color:"#999999",margin:"0 0 4px 4px"}}
						onClick={() => {
								setddata(["", ""]);
								setdshow(true);
						}}
					/>
				</div>
				<FloatingTextInput
						type="text"
						label="LifeInsurance"
						id="lifeInsurance"
						value={lifeInsurance}
						onChange={(num) => {
								const a = num.target.value.replace(/\D/g, '')
								setLifeInsurance(a);
						}}
				/>
				<div style={{ display: 'flex', flexDirection: 'row' }}>
					{chipset.map((item) => {
						return (
							<Chip
								className="chip-a"
								label={item.name}
								onClick={() => {
									setLifeInsurance(item.key)
								}}
							/>
						)
					})}
				</div>
				<div className="footer">
					<Footer
						navigation={props.history}
						next="/find-mintbox/risk-taking-capability/6"
						params={props.location.state}
						saveData={{
							riskCapability: {
								lifeInsurance,
							}
						}}
						nextdisabled={nextdisabled} 
						buttonText="Next &#8594;"/>
				</div>
			</div>
		</div>
	}
}
