import React, { useEffect, useRef, useState } from 'react'
import * as d3 from 'd3';
import cssVar from 'views/mobileView/styles';
import DataFrame from 'libs/DataFrame';

const GRAPH_MARGIN = 20;
const SVGHeight = 300;
const SVGWidth = window.innerWidth - window.innerWidth % 10;
const height = SVGHeight - 2 * GRAPH_MARGIN;
const width = SVGWidth - 2 * GRAPH_MARGIN;
const labelArea = 0;

export default function MultiLineChart() {
  const [graph, setgraph] = useState(Object.entries(data)[0][0]);
  const chart = useRef();

  const colors = [cssVar.getPropertyValue('--primary'), cssVar.getPropertyValue('--brandSecondary')];

  let groups = Object.entries(data).map(d => d[0]);
  let color  = d3.scaleOrdinal().domain(groups).range(colors);

  const make = (graph) => {

    d3.select(chart.current)
      .attr("width", width + labelArea)
      .attr("height", height + labelArea)
      .append("g")
      .attr("transform", `translate(${labelArea}, 0)`);
    
    // const x_axis = d3.axisBottom().scale(x);
    // const y_axis = d3.axisLeft().scale(y);;

    // svg.append('g').attr("transform", `translate(0, ${height - labelArea})`).call(x_axis).attr('class', 'x-axis');
    // svg.append('g').attr("transform", `translate(${labelArea}, 0)`).call(y_axis).attr('class', 'y-axis');

  }

  const update = (graph) => {
    const df = new DataFrame({data: data[graph]});

    const xDomain = [df.minXs(), df.maxXs()];
    const yDomain = [df.minYs(), df.maxYs()];

    const xRange = [labelArea, width - labelArea];
    const yRange = [height - labelArea, labelArea];

    const x = d3.scaleLinear().domain(xDomain).range(xRange);
    const y = d3.scaleLinear().domain(yDomain).range(yRange);

    const svg = d3.select(chart.current)
                .attr("width", width + labelArea)
                .attr("height", height + labelArea)
                .select('g')
                .attr("transform", `translate(${labelArea}, 0)`);

    let groupedData =  d3.group(data[graph], d => d.name);

    const line = (da) => d3.line().x(d => x(d.x)).y(d => y(d.y)).curve(d3.curveBasis)(da);

    // const x_axis = d3.axisBottom().scale(x);
    // const y_axis = d3.axisLeft().scale(y);;
 
    // svg.selectAll(".x-axis").transition().duration(1000).call(x_axis)
    // svg.selectAll(".y-axis").transition().duration(1000).call(y_axis)
  
    svg.selectAll(".line")
      .data(groupedData)
      .join("path")
      .attr("class", "line")
      .transition()
      .duration(1000)
      .attr("d", (d) => line(d[1]))
      .attr("fill", "none")
      .attr("stroke", (d) => color(d[0]))
      .attr("stroke-width", 2)
  }

  //  // This allows to find the closest X index of the mouse:
  //  var bisect = d3.bisector(function(d) { return d.x; }).left;

  //  // Create the circle that travels along the curve of chart
  //  var focus = svg.append('g')
  //                 .append('circle')
  //                   .style("fill", "none")
  //                   .attr("stroke", "black")
  //                   .attr('r', 8.5)
  //                   .style("opacity", 0)
 
  //  // Create the text that travels along the curve of chart
  //  var focusText = svg.append('g')
  //                     .append('text')
  //                       .style("opacity", 0)
  //                       .attr("text-anchor", "left")
  //                       .attr("alignment-baseline", "middle")
  
  //    // Create a rect on top of the svg area: this rectangle recovers mouse position
  // svg
  // .append('rect')
  // .style("fill", "none")
  // .style("pointer-events", "all")
  // .attr('width', width)
  // .attr('height', height)
  // .on('mouseover', mouseover)
  // .on('mousemove', mousemove)
  // .on('mouseout', mouseout);


// // What happens when the mouse move -> show the annotations at the right positions.
// function mouseover() {
//   focus.style("opacity", 1)
//   focusText.style("opacity",1)
// }

// function mousemove() {
//   // recover coordinate we need
//   var x0 = x.invert(d3.mouse(this)[0]);
//   var i = bisect(data, x0, 1);
//   selectedData = data[i]
//   focus
//     .attr("cx", x(selectedData.x))
//     .attr("cy", y(selectedData.y))
//   focusText
//     .html("x:" + selectedData.x + "  -  " + "y:" + selectedData.y)
//     .attr("x", x(selectedData.x)+15)
//     .attr("y", y(selectedData.y))
//   }
// function mouseout() {
//   focus.style("opacity", 0)
//   focusText.style("opacity", 0)
// }

  
  useEffect(() => {
    make(graph);
    update(graph);
  }, [])


  return (
    <>
      <div id='multiline-chart'>
        <svg ref={chart} />
      </div>
      <div className='mt-4' style={{ display: 'flex', flexDirection: 'row', justifyContent: 'space-between' }}>
        {Object.entries(data).map((d, i) => (
          <button
            key={`key-${i}`} 
            className='mx-1 multiline-chart-btn' 
            style={{ flex: 1 }} 
            onClick={() => update(d[0])} >
            {d[0]}
          </button>
        ))}
      </div>
    </>
  )
}

