import React from "react";
import { Redirect, Route, useHistory } from "react-router-dom";
import { toast } from "react-toastify";
import db from "../../../../../libs/db";
import BottomNavBar from "../../atoms/BottomNavBar";

function ProtectedRoutes({ component: Component, bottom_tab = true, ...restOfProps }) {
  const isAuthenticated = localStorage.getItem("user");
  const [profiled, setProfiled] = React.useState(false);
  var history = useHistory()
  const loadData = async () => {

    try {
      if(isAuthenticated){
        db.get(db.model.user, true).then((p) => {
          setProfiled(p.userData.profileComplete);
          if (isAuthenticated && p.userData.profileComplete && restOfProps.path.includes("complete_pro")) {
            history.push("/dashboard")
          }
        });
      }
     
    } catch (error) {
      toast.error("Try again ...");

    }
  };

  React.useEffect(() => {
    loadData();
  }, []);

  return (
    <Route
      {...restOfProps}
      render={(props) =>
        isAuthenticated ?
          <>
            {bottom_tab ? <BottomNavBar />
              : null}

            <Component {...props} />
          </>
          : <Redirect to="/email" />
      }
    />
  );
}

export default ProtectedRoutes;
