import React from 'react';
import { Modal, Spinner } from 'react-bootstrap';
import FloatingTextInput from 'views/mobileView/components/atoms/FloatingTextInput';
import Footer from '../Footer';
import Header from '../Header';
import { Chip } from '@mui/material';
import db from '../../../../../../libs/db';


export default function CapabilityPage3(props) {

	const chipset = [
		{ "key": "1200000", "name": "12 Lakh" },
		{ "key": "1800000", "name": "18 Lakh" },
		{ "key": "2400000", "name": "24 Lakh" }
	]
	const [loading, setloading] = React.useState(true);
	const [nextdisabled, setnextDisabled] = React.useState(true);

	const [income,setIncome]=React.useState("");
	
	const checkValidation = () => {
		if(income && income > 0){
			setnextDisabled(false);
		} else {
			setnextDisabled(true)
		}
	};

	React.useEffect(() => {
		checkValidation();
	})

	React.useEffect(() => {
		db.get(db.model.userData).then((u) => {
				console.log(u)
				if (u) {
						if (u.riskCapability) {
								setIncome(u.riskCapability.income || '')
						}
				}
				setloading(false);
		});
		checkValidation();
	}, []);

	if (loading) {
		return (
			<div className="container">
				<Spinner
					as="span"
					animation="grow"
					size="sm"
					role="status"
					aria-hidden="true"
				/>
			</div>
		);
	} else {
		return <div className="container">
			<Header headerText="Risk-taking capability" helperText="3/6 questions" navigation={props.history} />

			<div className="mt-4 mb-2">
				<div className="textHead">What is your annual income? <span className="textHelper">(before-tax)</span></div>
				<FloatingTextInput
						type="text"
						label="Income"
						id="income"
						value={income}
						onChange={(num) => {
								const a = num.target.value.replace(/\D/g, '')
								setIncome(a);
						}}
				/>
				<div style={{ display: 'flex', flexDirection: 'row' }}>
					{chipset.map((item) => {
						return (
							<Chip
								className="chip-a"
								label={item.name}
								onClick={() => {
									setIncome(item.key)
								}}
							/>
						)
					})}
				</div>
				<div className="footer">
					<Footer
						navigation={props.history}
						next="/find-mintbox/risk-taking-capability/4"
						params={props.location.state}
						saveData={{
							riskCapability: {
								income,
							}
						}}
						nextdisabled={nextdisabled} 
						buttonText="Next &#8594;"/>
				</div>
			</div>
		</div>
	}
}
