import React from 'react';
import { Button, Spinner } from 'react-bootstrap';
import db from '../../../../../../libs/db';
import { emailValidator } from '../../../../../../libs/validators';
import AdminService from '../../../../../../services/api/AdminServices';
import colorsFinal from '../../../../../../theme/colors';
import TabBar from '../../../../components/atoms/TabBar';
import MainHeader from '../../../../components/molecules/Headers/MainHeader';

export default function AddFamilyMemberScreen(prop) {
  const [members, setMembers] = React.useState([]);
  const [userData, setUserData] = React.useState(false);
  const [loading, setLoading] = React.useState(true);
  const [mobile, setMobile] = React.useState('');

  React.useEffect(() => {
    db.get(db.model.user, true)
      .then((user) => {
        setUserData(user.userData);
        AdminService
          .getClient(user.userData.id)
          .then((r) => {
            setMobile(JSON.parse(r.data)[0].Client[0].MobileNumber);
            setMembers(JSON.parse(r.data)[5].FamilyInfo);
            setLoading(false);
          })
          .catch((e) => console.log(e));
      });
  }, []);

  const Add = () => {
    const [email, setEmail] = React.useState('');
    const [showError, setShowError] = React.useState(false);
    const [showFaliure, setshowFaliure] = React.useState(false);
    const [showMemberExist, setshowMemberExist] = React.useState(false);
    const [addloading, setaddloading] = React.useState(false);

    const invalidInput = !emailValidator(email).isValid;

    const onValueChanged = (n) => {
      setEmail(n.target.value);
      setShowError(n.target.value !== '');
      setshowMemberExist(false);
      setshowFaliure(false);
    };

    const onContinuePressed = () => {
      setaddloading(true);
      if (!invalidInput) {
        const checkingMemberExist = new Promise((resolve, reject) => {
          AdminService.emailExist(email).then((e) => {
            setshowFaliure(e.data.ClientId === '');
          });
          let ex = false;
          members.forEach((member) => {
            if (member.Email === email) {
              setshowMemberExist(true);
              ex = true;
            }
          });
          resolve(ex);
        });

        checkingMemberExist.then((memberExists) => {
          if (!memberExists && !showFaliure) {
            AdminService.sendOTP(mobile, userData.email)
              .then((res) => {
                if (res.type) {
                  if (res.data.Result === 'success') {
                    setaddloading(false);
                    // navigation.navigate('AuthViaOTP', {
                    //   id: userData.id,
                    //   email,
                    //   clientEmail: userData.email,
                    //   clientPhone: mobile,
                    //   otp: res.data.otpcode
                    // });
                  } else {
                    setaddloading(false);
                    console.log('ERR : 2', res.msg);
                    // Toast.show({
                    //   text1: 'Error Occured!!',
                    //   text2: 'Try again ...',
                    //   type: 'error'
                    // });
                  }
                }
              });
          }
        });
      }
    };

    return (
      <div className='p-2' style={{ flex: 1 }}>
        <div style={{ flex: 1 }}>
          <div className='p-2'>
            <div className='textHead my-2'>Add a new member</div>
            <div className='textNormal my-2 pb-4' style={{ color: '#6C6C6C' }}>Start tracking a family members portfolio</div>
            <div className="floating-form ">
              <div className="floating-label">
                <input onChange={onValueChanged} className="floating-input" type="email" placeholder="Enter the email address of family member" />
                <span className="highlight"></span>
                <label className="floating-label-input" >Email</label>
              </div>
            </div>
            {showError && invalidInput ? (
              <span className='textHelper mt-3' style={{ color: colorsFinal.error }}>
                Invalid email address.
              </span>
            ) : (
              <>
                {showFaliure ? (
                  <span className='textHelper mt-3' style={{ color: colorsFinal.error }}>
                    Sorry, we couldn&lsquo;t find a mintbox user with that email address.
                  </span>
                ) : (false)}
                {showMemberExist ? (
                  <span className='textHelper mt-3' style={{ color: colorsFinal.error }}>
                    Member with this email address already exists.
                  </span>
                ) : (false)}
              </>
            )}
          </div>
          {addloading && (
            <div>
              <Spinner color={colorsFinal.brandPrimary} size='24' />
            </div>
          )}
          <div className='my-2' style={{ flex: 1, justifyContent: 'flex-end' }}>
            <Button onClick={onContinuePressed} className='btn-footer'
              style={{ opacity: !invalidInput ? 1 : 0.4, width: '100%', alignSelf: 'flex-end' }}>
              Continue
            </Button>
          </div>
        </div>
      </div>
    )
  }

  const Manage = () => {
    const onDeletePressed = (email) => {
      setLoading(true);
      AdminService
        .FamilyMemberAPIs
        .removeMember(userData.id, email)
        .then((r) => {
          if (r.type) {
            AdminService
              .getClient(userData.id)
              .then((q) => {
                setMembers(JSON.parse(q.data)[5].FamilyInfo);
                setLoading(false);
              })
              .catch((e) => setLoading(false));
          }
        })
        .catch((e) => console.log('Error: ', e));
    };

    const MemberCard = ({ name, email }) => (
      <div className='row my-2 mx-1 py-3 px-2' style={{ alignItems: 'center', border: '1px solid #E5E5E5', borderRadius: 10 }}>
        <div className='col-10 p-0'>
          <span className='textNormal'>{name}</span>
          <div className='textHelper pt-1' style={{ color: colorsFinal.placeholder }}>
            Email: <span className='textHelper pt-1' style={{ color: '#6C6C6C' }}>{email}</span>
          </div>
        </div>
        <div className='col-2 p-0 textNormal' onClick={() => onDeletePressed(email)} style={{ color: colorsFinal.error }}>
          Delete
        </div>
      </div>
    );

    return (
      <div className='container'>
        {members.length !== 0 ? (
          members.map((d, i) => (
            <MemberCard key={i} name={d.ClientName} email={d.Email} />
          )))
          : (
            <div className='my-3 textNormal' style={{ color: "#6C6C6C" }}>
              Add a family members to manage them.
            </div>
          )}
      </div>
    );
  }

  const keyTitles = [
    { key: 'Add', title: 'Add' },
    { key: 'Manage', title: 'Manage' }
  ];

  const keyComponents = { Manage, Add };

  return (
    <div>
      <MainHeader header="Add family member" navigation={prop.history} />
      <TabBar keyTitles={keyTitles} keyComponents={keyComponents} />
    </div>
  )
}
