import React, { useState } from "react";
import cssVar from "../../styles";
import FloatingTextInput from "./FloatingTextInput";

const DOBInputMask = (props) => {

    const [dateOfBirth, setDateOfBirth] = useState(props.defaultVal);
    const [isFocused, setisFocused] = React.useState(false);
    const [text, setText] = React.useState("");
    const [error, seterror] = React.useState(false);
    const [ErrorText, setErrorText] = React.useState("");


    function getAge(dateString) {
        dateString = dateString.split(" / ").reverse().join("/");
        var today = new Date();
        var birthDate = new Date(dateString);
        var age = today.getFullYear() - birthDate.getFullYear();
        var m = today.getMonth() - birthDate.getMonth();
        if (m < 0 || (m === 0 && today.getDate() < birthDate.getDate())) {
            age--;
        }
        return age;
    }

    const handleFocus = () => {
        setisFocused(true);
    };

    const handleBlur = () => {
        setisFocused(false);
        props.onBlur()
    };

    const checkValue = (str, max) => {
        if (str.charAt(0) !== "0" || str == "00") {
            var num = parseInt(str);
            if (isNaN(num) || num <= 0 || num > max) num = 1;
            str =
                num > parseInt(max.toString().charAt(0)) && num.toString().length == 1
                    ? "0" + num
                    : num.toString();
        }
        return str;
    };

    const DateChecker = (value) => {
        var age = getAge(value);
        if (age >= props.minAge && age <= props.maxAge) {
            setDateOfBirth(value);
        } else {
            seterror(true);
            setErrorText("Enter a valid DOB");
        }
    };

    const handleDateOfBirth = (value) => {
        seterror(false);
        setErrorText("");
        var input = value;
        let currentYear = new Date().getFullYear();
        if (/\D\/$/.test(input)) input = input.substr(0, input.length - 3);
        var values = input.split("/").map(function (v) {
            return v.replace(/\D/g, "");
        });
        if (values[0]) values[0] = checkValue(values[0], 31);
        if (values[1]) values[1] = checkValue(values[1], 12);
        if (values[2]) values[2] = checkValue(values[2], currentYear);
        var output = values.map(function (v, i) {
            return v.length == 2 && i < 2 ? v + " / " : v;
        });
        value = output.join("").substr(0, 14);
        if (value.length === 14 && parseInt(value.substr(10, 14)) < 1900) {
            var a = value.substr(10, 14).toString();
            value = value.replace(a, "");
        }
        if (props.dateCheck === true && value.length === 14) {
            DateChecker(value)
        }
        if (value.length === 14) {
            if (getAge(value) <= 0) {
                seterror(true);
                setErrorText("Enter a valid DOB");
            }
        }
        setDateOfBirth(value);
        props.setDOB(value);
    };

    const shouldShowLabel = () => text.length > 0;

    React.useEffect(() => {
        props.dateValidation(error)
    }, [error])

    return (
        <div>
            <FloatingTextInput
                id="dob"
                // type="number"
                placeholder="DD/MM/YYYY"
                label="Date of birth"
                value={dateOfBirth}
                onChange={(val) => handleDateOfBirth(val.target.value)}
                onFocus={handleFocus}
                onBlur={handleBlur}
                maxLength={14}
                spellCheck="false"
                autoCorrect="false"
            />
            {/* <div className="floating-label">
                <input
                    className="floating-input"
                    id="dob"
                    placeholder="DD/MM/YYYY"
                    value={dateOfBirth}
                    onChange={(val) => handleDateOfBirth(val.target.value)}
                    onFocus={handleFocus}
                    onBlur={handleBlur}
                    maxLength={14}
                    spellCheck="false"
                    autoCorrect="false"
                    style={{
                        color: cssVar.getPropertyValue('--color-text'),
                        borderBottomColor: error
                            ? cssVar.getPropertyValue('--red')
                            : isFocused
                                ? cssVar.getPropertyValue('--primary')
                                : cssVar.getPropertyValue('--divider'),
                        margin: 0,
                        padding: 0,
                        width: "100%",
                    }}
                //   errorstyle={{ display: "none", width: "100%", margin: 0, padding: 0 }}
                //   containerstyle={{
                //     position: "relative",
                //     width: "100%",

                //   }}
                /> */}
            {/* <input
        value={dateOfBirth}
        label={
          isFocused || shouldShowLabel()
            ? props.label + "  ( DD / MM / YYYY )"
            : ""
        }
        placeholder={
          !isFocused ? ("DD/MM/YYYY" ? "DD/MM/YYYY" : props.label) : ""
        }
        onFocus={handleFocus}
        onBlur={handleBlur}
        underlineColor={colorsFinal.primary}
        selectionColor={colorsFinal.primary}
        maxLength={14}
        returnKeyType="done"
        spellCheck="false"
        autoCorrect="false"
        labelstyle={{
          fontSize: cssVar.getPropertyValue('--font-normal'),
          color: error
            ? cssVar.getPropertyValue('--red')
            : isFocused
            ? cssVar.getPropertyValue('--primary')
            : cssVar.getPropertyValue('--placeholder'),
        }}
        placeholderColor={colorsFinal.placeholder}
        style={{
          color: cssVar.getPropertyValue('--color-text'),
          borderColor: colorsFinal.primary,
          margin: 0,
          padding: 0,
          width: "100%",
        }}
        inputcontainerstyle={{
          width: "100%",
          margin: 0,
          padding: 0,
          borderBottomColor: error
            ? colorsFinal.red
            : isFocused
            ? colorsFinal.primar
            : colorsFinal.placeholderColor,
        }}
        errorstyle={{ display: "none", width: "100%", margin: 0, padding: 0 }}
        containerstyle={{
          position: "relative",
          width: "100%",
          marginVertical: 10,
          paddingHorizontal: 0,
        }}
        onChange={(val) => handleDateOfBirth(val.target.value)}
      /> */}
            {/* <label
                    className="floating-label-input"
                    htmlFor="dob"
                    style={{
                        color: error
                            ? cssVar.getPropertyValue('--red')
                            : isFocused
                                ? cssVar.getPropertyValue('--primary')
                                : cssVar.getPropertyValue('--placeholder'),
                    }}
                >
                    Date of birth
                </label> */}

            {/* </div> */}

            {error ? (
                <span style={{ fontSize: cssVar.getPropertyValue('--font-helper'), color: cssVar.getPropertyValue('--red'), paddingTop: "40px" }}>
                    {ErrorText}
                </span>
            ) : null}
        </div>
    );
};
export default DOBInputMask;
