import { Chip, Stack } from '@mui/material';
import React, { useState } from 'react';
import { Button, Modal, Spinner } from 'react-bootstrap';
import { FiInfo } from 'react-icons/fi';
import FloatingTextInput from 'views/mobileView/components/atoms/FloatingTextInput';
import db from '../../../../../../libs/db';
import AdminServices from '../../../../../../services/api/AdminServices';
import colorsFinal from "../../../../../../theme/colors";
import Accordion from '../../../../components/atoms/Accordion';
import BottomBackNextBtn from '../../../../components/atoms/BottomBackNextBtn';
import { dropdownCustom } from '../../../../components/atoms/DataCustomizer';
import { getDDMMYYY } from '../../../../components/atoms/DOBFormatter';
import DOBInputMask from '../../../../components/atoms/DOBInputMask';
import ProfileCompleteHeader from '../../../../components/molecules/Headers/ProfileCompletionHeader';
import cssVar from '../../../../styles';


export default function Nominee(props) {

	const chipset = [
		{ "key": "a", "name": "Father" },
		{ "key": "b", "name": "Mother" },
		{ "key": "c", "name": "Spouse" },
		{ "key": "d", "name": "Son" },
		{ "key": "e", "name": "Daughter" },
	]

	const [jointyes, setjointYes] = React.useState('no');
	const [addnomine, setaddnomine] = React.useState('no');
	const [nomDataArr, setnomiDataArr] = React.useState([{
		noName: '',
		relation: '',
		guardName: '',
		dob: '',
		nomAllocate: 0
	}]);

	const [joint, setJoint] = React.useState('joint');
	const [name, setname] = React.useState('');
	const [dobv, setdobv] = React.useState('');
	const [income, setincome] = React.useState('');
	const [wealth, setwealth] = React.useState('');
	const [occupation, setoccupation] = React.useState('');

	const [noName, setnoName] = React.useState('');
	const [relation, setrelation] = React.useState('');
	const [guardName, setGuardName] = React.useState('');
	const [dob, setdob] = React.useState('');

	//   const [val, setVal] = React.useState('');
	//   onClick = (val) =>{
	//     console.log(val);
	// };

	const [nextdisabled, setnextDisabled] = React.useState(true);
	const [loading, setloading] = React.useState(true);
	const [nomloading, setnmloading] = React.useState(false);

	// dialog
	const [dshow, setdshow] = React.useState(false);
	const [ddata, setddata] = React.useState([]);
	const [dtitle, setdtitle] = React.useState('');
	const [dimg, setdimg] = React.useState('');

	const [isDatePickerVisible, setDatePickerVisibility] = React.useState(false);
	const [activeNum, setactiveNum] = React.useState(0);

	const showDatePicker = () => {
		setDatePickerVisibility(true);
	};

	const hideDatePicker = () => {
		setDatePickerVisibility(false);
	};
	function getAge(dateString) {
		dateString = dateString.split(" / ").reverse().join("/");
		var today = new Date();
		var birthDate = new Date(dateString);
		var age = today.getFullYear() - birthDate.getFullYear();
		var m = today.getMonth() - birthDate.getMonth();
		if (m < 0 || (m === 0 && today.getDate() < birthDate.getDate())) {
			age--;
		}
		return age;
	}
	const handleConfirm = (date) => {
		console.log('A date has been picked: ', getDDMMYYY(date));
		setdob(date);
		hideDatePicker();
	};

	const [incomeList, setincomeList] = React.useState([]);
	const [wealthList, setwealthList] = React.useState([]);
	const [occupationList, setoccupationList] = React.useState([]);




	const getData = async () => {
		try {
			await AdminServices.getOccupation()
				.then(async (res) => {
					// console.log('data1: ', res);
					const data = await dropdownCustom(res.data, ['UID', 'Occupation']);
					setoccupationList(data);
				})
				.then(async () => {
					await AdminServices.getAnnualIncome()
						.then(async (res) => {
							// console.log('data2: ', res);

							const data = await dropdownCustom(res.data, [
								'UID',
								'IncomeSlab'
							]);
							setincomeList(data);
						})
						.then(async () => {
							await AdminServices.getSourceofWealth()
								.then(async (res) => {
									// console.log('data3: ', res);

									const data = await dropdownCustom(res.data, [
										'UID',
										'Source'
									]);
									setwealthList(data);
								})
								.then(() => {
									setloading(false);
								});
						});
				});
		} catch (error) {
			console.log(error);
		}
	};

	const checkValidation = () => {
		if (addnomine === 'no') {
			setnextDisabled(false);
		} else if (addnomine !== 'no') {
			if (nomDataArr.length === 0) {
				setnextDisabled(true);
			}
			for (var i = 0; i < nomDataArr.length; i++) {
				console.log("h1", nomDataArr[i].noName)
				if (nomDataArr[i].noName !== '' && nomDataArr[i].relation !== '' && nomDataArr[i].dob !== '') {
					if (getAge(nomDataArr[i].dob) < 19) {
						if (nomDataArr[i].guardName !== '') {
							setnextDisabled(false);

						} else {
							setnextDisabled(true);
							break;
						}
					} else {
						setnextDisabled(false);
					}
				} else {
					setnextDisabled(true);
					break;
				}
			}
		}
	};


	const dataup = () => {
		var a = nomDataArr
		if (a.length === 1) {
			a[0].nomAllocate = 100
		} else if (a.length === 2) {
			a[0].nomAllocate = 50
			a[1].nomAllocate = 50
		} else if (a.length === 3) {
			a[0].nomAllocate = 34
			a[1].nomAllocate = 33
			a[2].nomAllocate = 33
		}
		return a
	}

	React.useEffect(() => {
		setnmloading(false)
		checkValidation()
	}, [nomloading])

	const addNominee = async () => {

		let a = nomDataArr;
		if (a.length <= 2) {
			a.push({
				noName: "",
				relation: "",
				guardName: "",
				dob: '',
				nomAllocate: 0
			})
			await setnomiDataArr(a)
			setactiveNum(nomDataArr.length - 1)
			setnmloading(true)
		}
	}

	const delNominee = (index) => {
		var a = nomDataArr;
		a.splice(index, 1)
		setnomiDataArr(a)
		setnmloading(true)
	}

	const NomineDetails = (data) => {

		console.log(data)

		const [noName, setnoName] = useState(data.noName);
		const [relation, setrelation] = useState(data.relation);
		const [guardName, setGuardName] = useState(data.guardName);
		const [dob, setdob] = useState(data.dob);

		return <Accordion index={data.index}
			active={data.index === activeNum ? true : false}
			dividerStyle={{ borderBottomWidth: 0.1 }}
			title={<span className="textNormal">Nominee {data.index + 1} </span>}
			secTitle={<span style={{ color: colorsFinal.placeholder }}>{<br></br>}{nomDataArr[data.index].noName} </span>}
			left={null}
			// right={() => {
			// 	return <span className="textNormal" style={{ color: cssVar.getPropertyValue('--red') }} onClick={() => {
			// 		delNominee(data.index);
			// 	}}>Remove</span>
			// }}
			right={() =>
				data.index === activeNum ? (<span className="textNormal" style={{ color: cssVar.getPropertyValue('--red') }} onClick={() => {
					delNominee(data.index);
				}}>Remove</span>) : (<span className="textNormal" style={{ color: cssVar.getPropertyValue('--primary') }} onClick={() => {
					setactiveNum(data.index)
				}}>Edit</span>)
			}
			updateExpand={(i, val) => {
				console.log(i, val)
				if (val) {
					setactiveNum(i)
				} else {
					setactiveNum('')
				}
			}}
		>
			<div style={{ width: '100%' }} >
				<FloatingTextInput
					type="text"
					id="full name"
					label="Full name"
					value={noName}
					// onFocus={() => checkValidation()}
					// onBlur={() => checkValidation()}
					onChange={(text) => {
						var aa = nomDataArr
						aa[data.index].noName = text.target.value
						setnomiDataArr(aa)
						setnoName(text.target.value)
						console.log(text.target.value)
					}}
				/>

				<FloatingTextInput
					value={relation}
					type="text"
					autoCapitalize="words"
					id="relation"
					label="Relationship with investor"
					onBlur={() => checkValidation()}
					onChange={(text) => {
						var aa = nomDataArr
						aa[data.index].relation = text.target.value
						setnomiDataArr(aa)
						setrelation(text.target.value)
					}}
				/>
				<div style={{ display: 'flex', flexDirection: 'row', justifyContent: 'space-between' }}>
					{chipset.map((item) => {
						return (
							<Stack direction="row" spacing={1}>
								<Chip
									// size="small"
									className="chip-a"
									// style={{ fontSize: cssVar.getPropertyValue('--font-helper') }}
									label={item.name}
									variant="outlined"
									onClick={() => {
										var aa = nomDataArr
										aa[data.index].relation = item.name
										setnomiDataArr(aa)
										setrelation(item.name)
									}} />
							</Stack>
						)
					})}
				</div>
				<DOBInputMask
					dateCheck
					defaultVal={dob}
					dobof={props.location.state.selection}
					minAge={0}
					maxAge={200}
					dateValidation={(dobvv) => setdobv(dobvv)}
					setDOB={(dobb) => {
						// console.log(dob);
						var aa = nomDataArr
						aa[data.index].dob = dobb
						setnomiDataArr(aa)
						setdob(dobb);
					}}
					onBlur={() => checkValidation()}
				/>
				{getAge(dob) < 19 ? (
					<FloatingTextInput
						type="text"
						id="guard name"
						autoCapitalize="words"
						label="Guardian name"
						onBlur={() => checkValidation()}
						value={guardName}
						onChange={(text) => {
							var aa = nomDataArr
							aa[data.index].guardName = text.target.value
							setnomiDataArr(aa)
							setGuardName(text.target.value)
							console.log(text.target.value)
						}}
					/>
				) : null}
			</div>
		</Accordion>
	}

	React.useEffect(() => {
		checkValidation();
	});

	React.useEffect(() => {
		db.get(db.model.userData).then((u) => {
			if (u) {
				// u = JSON.parse(u);

				const r1 = u.joint;
				// setoccupationList(u.fatca.occupationList || []);
				// setincomeList(u.fatca.incomeList || []);
				// setwealthList(u.fatca.wealthList || []);
				if (r1) {
					const r2 = u.joint.jointData;
					if (r2.nominevalue === 'yes') {
						setaddnomine(r2.nominevalue || '')
						setnomiDataArr(r2.nomineData || [])
					} else {
						setaddnomine(r2.nominevalue || '');
					}
				}
				setloading(false);
			}
		});
		checkValidation();
	}, []);

	if (loading) {
		return (
			<div className="container">
				<Spinner
					as="span"
					animation="grow"
					size="sm"
					role="status"
					aria-hidden="true"
				/>
			</div>
		);
	} else {
		return <div className="container">
			<ProfileCompleteHeader header="Nominee Details" navigation={props.history} percentage={50} />
			<Modal
				show={dshow}
				onHide={() => setdshow(!dshow)}
				backdrop="static"
				keyboard={false}
				size="sm"
				aria-labelledby="contained-modal-title-vcenter"
				centered
			>
				<Modal.Body>
					<p className="textHead">{ddata[0]}</p>
					<p className="textNormal">{ddata[1]}</p>
				</Modal.Body>
				<Button variant="light" style={{ color: cssVar.getPropertyValue('--primary') }} onClick={() => setdshow(!dshow)}>
					Okay
				</Button>
			</Modal>
			<div className="mt-4 mb-2"
			>
				<span className="textHeadauth ">Do you wish to add a nominee?</span>
				<FiInfo
					className="mb-1 mx-2"
					size={15}
					color={cssVar.getPropertyValue("--primary")}
					onClick={() => {
						setddata(["", "It is strongly recommended that you add a nominee. All your investments will be transferred to the nominee after your death and will help settle claims faster."]);
						setdshow(true);
					}}
				/>
			</div>

			<div className="radio-item">
				<input
					type="radio"
					id="ritem"
					name="ritem"
					value="yes"
					onChange={(val) => {
						setaddnomine(val.target.value)
						console.log(val.target.value)
					}}
					className="textNormal"
					checked={addnomine === "yes"}
				/>
				<label className="textNormal radio-label" htmlFor="ritem">Yes</label>
			</div>
			<div className="radio-item">
				<input
					type="radio"
					id="ritemb"
					name="ritem"
					value="no"
					onChange={(val) => {
						setaddnomine(val.target.value)
						console.log(val.target.value)
					}}
					className="textNormal"
					checked={addnomine === "no"}
				/>
				<label className="textNormal radio-label" htmlFor="ritemb">No</label>
			</div>
			{addnomine === 'yes' ? (
				<div style={{ marginBottom: "50px", display: "flex", flexDirection: "column" }}>
					{nomloading ? null :
						nomDataArr.map((data, index) => {
							return <NomineDetails {...data} index={index} />
						})}{<br />}

					{nomDataArr.length > 2 ? (null) : (
						<>
							<span style={{ fontSize: cssVar.getPropertyValue('--font-normal'), color: cssVar.getPropertyValue('--primary') }} onClick={() => { addNominee(); }}>Add {nomDataArr.length === 0 ? "" : "another "}nominee</span>
							<span style={{ fontFamily: 'DMSans-Regular', fontSize: "10px", color: cssVar.getPropertyValue('--textLight'), marginBottom: "50px" }} >You can add up to 3 nominees</span>
						</>
					)}
				</div>
			) : null}
			<div className="footer">
				<BottomBackNextBtn
					next={addnomine === 'yes' ? "/complete_pro/allocation" : "/complete_pro/bank"}
					navigation={props.history}
					params={props.location.state}
					saveData={{
						joint: {
							jointData: {
								jointvalue: jointyes,
								jointUser: {
									name,
									dob,
									fatca: {
										income,
										wealth,
										occupation
									},
									accountType: joint
								},
								nominevalue: addnomine,
								nomineData: dataup()
							}
						}
					}}
					nextdisabled={nextdisabled} />
			</div>
		</div>
	}
}

