import React, { Component, useState } from "react";
import { AiOutlinePlus, AiOutlineMinus } from 'react-icons/ai';

function NumericInput({ style = {}, value = '', id = '', onLimitReached = '', onChange, minValue = 'none', maxValue = 'none' }) {

    const [count, setCount] = useState(value);

    const decrease = () => {
        setCount(Number(count) - 1);
        onChange(Number(count) - 1)
    }

    const increase = () => {
        setCount(Number(count) + 1);
        onChange(Number(count) + 1)

    }


    return (
        <div style={{ display: "flex", flex: 1, justifyContent: "center", }}>
            <span className="btn-numeric-L" ><AiOutlineMinus onClick={decrease} className="btn-numeric-l" size={15} /></span>
            <input
                id={id}
                onLimitReached={onLimitReached}
                disabled
                className="num-input"
                name="quantity"
                value={count}
                style={style}
                minValue={minValue}
                maxValue={maxValue}
                type="number"
            />
            <span className="btn-numeric-R" ><AiOutlinePlus onClick={increase} className="btn-numeric-r" size={15} /></span>
        </div>
    );

}

export default NumericInput;