import FlatList from 'flatlist-react';
import React from 'react';
import cssVar from 'views/mobileView/styles';



export default function Slider(props) {
  const [activeIndex, setactiveIndex] = React.useState(0);
  const { data, dotClickEnabled, renderItem, renderPagination, activeDotStyle,
          dotStyle, onSwipe, keyExtractor, pagingEnabled = true,
          showsHorizontalScrollIndicator = false, style } = props;

  const refs = React.useRef({ activeIndex: 0 });
  const setRef = (name) => {
    refs.current[0] = name;
  };
  const viewabilityConfig = React.useRef({
    viewAreaCoveragePercentThreshold: 100
  });

//   const rtlSafeIndex = (i, d) => (isAndroidRTL ? d.length - 1 - i : i);

  const skeyExtractor = (item, index) => index.toString();

  const onSwipePageChange = React.useRef((info) => {
    if (info.viewableItems[0]) {
      const currentPage = info.viewableItems[0].index;
      if (onSwipe) { onSwipe(currentPage); }
      setactiveIndex(currentPage);
    }
  });

  const goToSlide = (pageNum) => {
    refs.current[0].scrollToIndex({ animated: false, index: pageNum });
    setactiveIndex(pageNum);
    if (onSwipe) { onSwipe(pageNum); }
  };

  const Dots = () => {
    const dots = Array(data.length).fill(0);

    return (
      <div 
    //   style={styles.dotsContainer}
      >
        {dots.map((_, i) => (dotClickEnabled ? (
          <div key={i} onPress={() => { goToSlide(i); }}
            // style={[styles.dot, rtlSafeIndex(i) === activeIndex ? activeDotStyle : dotStyle]} 
            />
        ) : (
          <div key={i}
            // style={[styles.dot, rtlSafeIndex(i) === activeIndex ? activeDotStyle : dotStyle]}
             />
        )
        ))}
      </div>
    );
  };

  return (
    <>
      <FlatList
        ref={(ref) => setRef(ref)}
        data={data}
        // style={{ ...styles.flatList, ...style }}
        pagingEnabled={pagingEnabled}
        horizontal
        showsHorizontalScrollIndicator={showsHorizontalScrollIndicator}
        renderItem={renderItem}
        keyExtractor={keyExtractor || skeyExtractor}
        onViewableItemsChanged={onSwipePageChange.current}
        viewabilityConfig={viewabilityConfig.current}
        initialNumToRender={1} />
      {renderPagination === false ? (true)
        : renderPagination ? (renderPagination(activeIndex)) : (<Dots />)}
    </>
  );
}

