//     display: 'flex',
//     flexDirection: 'row',
//     alignItems: 'center',
//     justifyContent: 'center',
//     bottom: 10,
//     width: '100%'
//   }
// });

Slider.defaultProps = {
  activeDotStyle: { backgroundColor: `${cssVar.getPropertyValue('--primary')}55` },
  dotStyle: { backgroundColor: 'rgba(100, 100, 100, .5)' },
  dotClickEnabled: true,
  pagingEnabled: true,
  showsHorizontalScrollIndicator: false
};