import { Button } from 'react-bootstrap';
import React from 'react';
import db from '../../../../../libs/db';
import { setUserData } from '../../../../../redux/CompleteProfile/actions';

const Footer = ({
  next,
  navigation,
  params,
  nextdisabled,
  saveData,
  buttonText
}) => {

  const onNextPress = () => {
    if (saveData && !nextdisabled) {  
      db.get(db.model.userData).then((u) => {
        if (u) {
          if(u.riskCapability){
            Object.assign(u.riskCapability, saveData.riskCapability);
          } else {
            Object.assign(u, saveData);
          }
        } else{  
          u = saveData
        }
        console.log(u)
        setUserData(u);
       navigation.push({pathname: next, state : params})
      });
    } else {
        navigation.push({pathname: next, state : params})
    }
  };

  return (
    <div
      style={{
        display: 'flex',
        flexDirection: 'row',
        flex: 1,
        justifyContent: 'space-between',
        width: '100%'
      }}>
        <Button
          variant="primary"
          width="100%"
          className="btn-footer"
          style={{ opacity: nextdisabled ? 0.4 : 1 }}
          onClick={() => (nextdisabled ? {} : onNextPress())}
        >
          {buttonText}
        </Button>
    </div>
  );
};

export default Footer;
