import React from 'react';
import { Modal, Spinner } from 'react-bootstrap';
import FloatingTextInput from 'views/mobileView/components/atoms/FloatingTextInput';
import Footer from '../Footer';
import Header from '../Header';
import { Chip } from '@mui/material';
import db from '../../../../../../libs/db';


export default function CapabilityPage1(props) {

	const chipset = [
		{ "key": "25", "name": "25 years" },
		{ "key": "35", "name": "35 years" },
		{ "key": "45", "name": "45 years" }
	]
	const [loading, setloading] = React.useState(true);
	const [nextdisabled, setnextDisabled] = React.useState(true);

	const [age,setAge]=React.useState("");
	
	const checkValidation = () => {
		if(age && age > 0){
			setnextDisabled(false);
		} else {
			setnextDisabled(true)
		}
	};

	React.useEffect(() => {
		checkValidation();
	})

	React.useEffect(() => {
		db.get(db.model.userData).then((u) => {
				console.log(u)
				if (u) {
						if (u.riskCapability) {
								setAge(u.riskCapability.age || '')
						}
				}
				setloading(false);
		});
		checkValidation();
	}, []);

	if (loading) {
		return (
			<div className="container">
				<Spinner
					as="span"
					animation="grow"
					size="sm"
					role="status"
					aria-hidden="true"
				/>
			</div>
		);
	} else {
		return <div className="container">
			<Header headerText="Risk-taking capability" helperText="1/6 questions" navigation={props.history} />

			<div className="mt-4 mb-2">
				<div className="textHead">What is your present age?</div>
				<FloatingTextInput
						type="text"
						label="Age"
						id="age"
						value={age}
						onChange={(num) => {
								const a = num.target.value.replace(/\D/g, '')
								setAge(a);
								console.log(a)
						}}
				/>
				<div style={{ display: 'flex', flexDirection: 'row'}}>
					{chipset.map((item) => {
						return (
							<Chip
								className="chip-a"
								label={item.name}
								onClick={() => {
									setAge(item.key)
								}}
							/>
						)
					})}
				</div>
				<div className="footer">
					<Footer
						navigation={props.history}
						next="/find-mintbox/risk-taking-capability/2"
						params={props.location.state}
						saveData={{
							riskCapability: {
								age,
							}
						}}
						nextdisabled={nextdisabled} 
						buttonText="Next &#8594;"/>
				</div>
			</div>
		</div>
	}
}
