                                    borderBottom: `1px solid ${cssVar.getPropertyValue('--divider')}`,
                                    borderTop: `1px solid ${cssVar.getPropertyValue('--divider')}`,
                                    borderRadius: "0px",
                                    borderLeft: `0px solid ${cssVar.getPropertyValue('--divider')}`,
