import { ManWithLadder } from 'assets/SVG';
import React from 'react';
import { Button } from 'react-bootstrap';
import InfoCard from 'views/mobileView/components/molecules/Cards/InfoCard';
import MainHeader from 'views/mobileView/components/molecules/Headers/MainHeader';
import cssVar from 'views/mobileView/styles';



export default function PurposeOfInvestment(props) {


	const [purpose, setPurpose] = React.useState('');

	return (
		<div style={{ flex: 1 }}>
			<MainHeader
				header={<span style={{ fontSize: cssVar.getPropertyValue('--font-head') }}>Invest in <span style={{ color: cssVar.getPropertyValue('--primary') }}>mintbox</span></span>}
				navigation={props.history}
			/>
			<div>
				<InfoCard
					svg={ManWithLadder}
					title="Lets find you a mintbox!"
					text="The purpose of your investment is the starting point to finding the best mintbox for you."
					gradient={{
						colors: ['#F2FBFB88', '#F2FBFB']
					}} />
				<div className="container">
					<h3 className="textHead">What is the purpose of your investment?</h3>
					<div className="radio-item">
						<input
							type="radio"
							id="ritem1"
							name="ritem"

							value="Long term wealth creation"
							onChange={(v) => setPurpose(v.target.value)}
							className="textNormal"
						/>
						<label className="textNormal radio-label" htmlFor="ritem1"><span>Long term wealth creation <span style={{ fontSize: cssVar.getPropertyValue('--font-helper'), color: cssVar.getPropertyValue('--textLight') }}>(5+ years)</span></span></label>
					</div>
					<div className="radio-item">
						<input
							type="radio"
							id="ritem2"
							name="ritem"

							value="Achieve a financial goal"
							onChange={(v) => setPurpose(v.target.value)}
							className="textNormal"
						/>
						<label className="textNormal radio-label" htmlFor="ritem2">Achieve a financial goal</label>
					</div>
					<div className="radio-item">
						<input
							type="radio"
							id="ritem3"
							name="ritem"

							value="Generate monthly cash flow"
							onChange={(v) => setPurpose(v.target.value)}
							className="textNormal"
						/>
						<label className="textNormal radio-label" htmlFor="ritem3">Generate monthly cash flow</label>
					</div>
					<div className="radio-item">
						<input
							type="radio"
							id="ritem4"
							name="ritem"

							value="Beat inflation with low volatility"
							onChange={(v) => setPurpose(v.target.value)}
							className="textNormal"
						/>
						<label className="textNormal radio-label" htmlFor="ritem4">Beat inflation with low volatility</label>
					</div>
				</div>
			</div>

			<div className="footer" style={{bottom: "50px"}}>
				<Button
					variant="primary"
					className="btn-footer"
					disabled={purpose === ''}
					onClick={() => {
						switch (purpose) {
							case 'Long term wealth creation':
								props.history.push('/invest/horizon');
								break;
							case 'Achieve a financial goal':
								props.history.push('/portfolio');
								break;
							case 'Beat inflation with low volatility':
								props.history.push('/portfolio');
								break;
							default:
								break;
						}
					}}
				>
					Next
				</Button>
			</div>
		</div>
	);
}
