import React from 'react';
import { Spinner } from 'react-bootstrap';
import Footer from '../Footer';
import Header from '../Header';
import db from '../../../../../../libs/db';

export default function CapabilityPage6(props) {
	const RadioInputOptions = [
		{ key: '0', name: 'Yes' },
		{ key: '1', name: 'No' }
]
	const [loading, setloading] = React.useState(true);
	const [nextdisabled, setnextDisabled] = React.useState(true);

	const [medicalInsurance,setMedicalInsurance]=React.useState("");
	// dialog
	const [dshow, setdshow] = React.useState(false);
	const [ddata, setddata] = React.useState([]);
	const checkValidation = () => {
		if(medicalInsurance){
			setnextDisabled(false);
		} else {
			setnextDisabled(true)
		}
	};

	React.useEffect(() => {
		checkValidation();
	})

	React.useEffect(() => {
		db.get(db.model.userData).then((u) => {
				console.log(u)
				if (u) {
						if (u.riskCapability) {
							setMedicalInsurance(u.riskCapability.medicalInsurance || '')
						}
				}
				setloading(false);
		});
		checkValidation();
	}, []);

	if (loading) {
		return (
			<div className="container">
				<Spinner
					as="span"
					animation="grow"
					size="sm"
					role="status"
					aria-hidden="true"
				/>
			</div>
		);
	} else {
		return <div className="container">
			<Header headerText="Risk-taking capability" helperText="6/6 questions" navigation={props.history} />
			<div className="mt-4 mb-2">
				<div className="textHead" style={{marginBottom:'4%'}}>Do you and all your financial dependents have medical insurances?</div>
				{RadioInputOptions.map((obj) => (
                    <div key={obj.key} style={{ display: 'flex', flexDirection: 'row' }}>
                        <div className="radio-item">
                            <input
                                type="radio"
                                id={obj.key}
                                name="ritem"
                                value={obj.key}
                                onChange={(val) => {
                                    setMedicalInsurance(val.target.value)
                                    console.log(val.target.value)
                                }}
                                className="textNormal"
                                checked={medicalInsurance === obj.key}
                            />
                            <label className="textNormal radio-label" htmlFor={obj.key}>{obj.name}</label>
                        </div>
                    </div>
                ))}
				<div className="footer">
					<Footer
						navigation={props.history}
						next="/find-mintbox/risk-taking-capability/result"
						params={props.location.state}
						saveData={{
							riskCapability: {
								medicalInsurance,
							}
						}}
						nextdisabled={nextdisabled} 
						buttonText="Show my risk-taking capability"/>
				</div>
			</div>
		</div>
	}
}
