let lastItem = {};
export const ArrayScrollLoader = (arr, qty) => new Promise((resolve, reject) => {
  let newArr;
  const lastItemIndex = arr.indexOf(lastItem);
  if (lastItemIndex === arr.length - 1) return resolve('done');
  if (Object.entries(lastItem).length === 0) {
    newArr = arr.slice(0, qty);

    lastItem = newArr.pop();
  } else {
    const newIndex = arr.indexOf(lastItem) + 1;
    newArr = arr.slice(newIndex, qty + newIndex);
    lastItem = newArr.pop();
  }
  setTimeout(() => {
    resolve(newArr);
  }, 200);
});
