import React from 'react';
import { ProgressBar } from 'react-bootstrap';
import { FiArrowLeft } from "react-icons/fi";
import cssVar from '../../../styles';

function ProfileCompleteHeader({
   screenType,
   percentage,
   header,
   navigation,
   showInfo,
   goBack
}) {
   return (
      <div>
         <div className="d-flex flex-row justify-content-between align-items-center pt-2">
            {screenType === 'Home' ? null : (
               <FiArrowLeft style={{ marginRight: "10px", color: cssVar.getPropertyValue('--color-text') }} size={20} onClick={() => navigation.goBack()} />
            )}
            <span style={{ flex: 1, fontSize: cssVar.getPropertyValue('--font-authHead') }} className="textHead">{header} {showInfo}</span>

            { }
            <span
               onClick={() => navigation.push('/dashboard')}
               className="textNormal" style={{ color: cssVar.getPropertyValue('--primary') }}>Exit</span>
         </div>
         <div className="Progress">
            <style type="text/css">
               {`
					.progress-bar {
						background-color: #00A6A4;
						color: white;
                  border-radius: 10px !important;
					}
				`}
            </style>
            <ProgressBar now={percentage} style={{ borderRadius: 10 }} className="progress-bar-custom" />
         </div>
      </div>
   );
};

export default ProfileCompleteHeader;
